import React from 'react';
import { Container } from 'react-bootstrap';
import '../styles/toggle_Body.css';
import { useState } from 'react';
import FemaleImg from '../styles/Group 7568.svg';
import MaleImg from '../styles/Group 7567.svg';
import MaleBody from './HumanBody/MaleBody';
import FemaleBody from './HumanBody/FemaleBody';

function Human_Body() {
  const [toggle, setToggle] = useState(false);
  // console.log(toggle);
  return (
    <>
      <Container
        className='human_body_container'
        style={
          {
            // marginLeft: "55%",
          }
        }
      >
        <div
          class='button b2'
          id='button-10'
          onClick={() => setToggle(!toggle)}
        >
          <input type='checkbox' class='checkbox' />
          <div class='knobs'>
            <span>Male</span>
          </div>
          <div class='layer'></div>
        </div>
        <div>
          {toggle ? <FemaleBody /> : <MaleBody />}
          {/* <img
            src={toggle ? FemaleImg : MaleImg}
            alt={toggle ? 'female' : 'male'}
            style={{ marginLeft: '30%' }}
          /> */}
        </div>
      </Container>
    </>
  );
}

export default Human_Body;
