import "./App.css";
import { Switch, Route, Redirect } from "react-router-dom";
import { createContext, useEffect, useLayoutEffect, useState } from "react";
import "./landingPage/styles/responsive.css";
import "./fontAwesome";
import "bootstrap/dist/css/bootstrap.min.css";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Main_page from "./landingPage/components/main_page";
import Main_hospital from "./hospitalPage/components/main_hospital";
import axios from "axios";
import Register from "./SignUp_Login/Register/index";
import Signup from "./signup/Signup";
import AdminHs from "./Admin/RenderAdmin";
import MainEmergency from "./emergencyPage/components/main-emergency";
import Search from "./Search/components/Search";
import CreateHospital from "./createHospital/CreateHospital";
import CreateHospitalTemp from "./createHospitalTemp/CreateHospital";
import MainAmbulance from "./Ambulance/Components/mainambulance";
import SuperAdminDashboard from "./SuperAdminDashboard/AdminDashboard";

import CreateForm from "./createHospital/createForm";
import CreateAmbulance from "./Ambulance Form/CreateAmbulance";

// import Profile from "./SuperAdminDashboard/Dashboard/Profile";

// import SearchDemo from "./SearchDemo/SearchDemo";

import DynamicSideNavbar from "./AdminDashboard/Components/DynamicSideNavbar";
import Human from "./Human_Body_Illustration/components/main";
import AmbulanceTable from "./Ambulance Form/Ambulance Table/AmbulanceTable";
// import Feedback from "./Feedback/Feedback";

// import DoctorTimings from "./AdminDashboard/Components/DoctorDetails/DoctorTimings";

export const LocationContext = createContext();

function App() {
  axios.defaults.withCredentials = true;
  // axios.defaults.headers.common['location'] = [20.3010312, 85.7504126];

  const [address, setAddress] = useState();
  const [location, setLocation] = useState([20.3010312, 85.7504126]);
  const [isCoords, setIsCoords] = useState(false);

  useLayoutEffect(() => {
    const showLocation = (position) => {
      let latitude = position.coords.latitude;
      let longitude = position.coords.longitude;
      setLocation([latitude, longitude]);
      setIsCoords(true);

      // get current address
      axios
        .get(`/api/hospital/address?lat=${latitude}&lng=${longitude}`)
        .then((res) => {
          setAddress(
            res.data?.address?.plus_code?.compound_code
              .split(",")?.[0]
              .split(" ")?.[1]
          );
        })
        .catch((err) => console.log(err.response));
    };
    const errorHandler = (err) => {
      setIsCoords(false);
      setLocation([20.3010312, 85.7504126]);

      if (err.code == 1) {
        alert("Error: Location Access is denied!");
      } else if (err.code == 2) {
        alert("Error: Position is unavailable!");
      }
    };
    const getLocation = () => {
      if (navigator.geolocation) {
        // timeout at 60000 milliseconds (60 seconds)
        var options = { enableHighAccuracy: true, timeout: 60000 };
        navigator.geolocation.getCurrentPosition(
          showLocation,
          errorHandler,
          options
        );
      } else {
        alert("Sorry, browser does not support geolocation!");
      }
    };
    getLocation();
  }, []);

  return (
    <LocationContext.Provider
      value={{ address, isCoords, setIsCoords, location }}
    >
      <Switch>
        <Route exact path="/" component={Main_page} />
        <Route exact path="/search" component={Search} />
        <Route exact path="/search/:category/:item" component={Search} />
        <Route exact path="/hospital/details/:id" component={Main_hospital} />
        <Route exact path="/create" component={CreateForm} />
        <Route exact path="/signup" component={Signup} />
        <Route exact path="/register" component={Register} />
        <Route exact path="/admin" component={AdminHs} />
        <Route exact path="/emergency" component={MainEmergency} />

        <Route exact path="/ambulance" component={MainAmbulance} />
        <Route exact path="/ambulance-form" component={CreateAmbulance} />
        <Route exact path="/ambulance-table" component={AmbulanceTable} />
        {/* <Route exact path="/feedback" component={Feedback} /> */}

        {/* <Route exact path="/ambulance" component={Main_page} /> */}

        <Route exact path="/dynamicNav" component={DynamicSideNavbar} />

        <Route exact path="/human" component={Human} />
        {/* <Route exact path="/searchdemo" component={SearchDemo} /> */}
        {/* Temp Routes */}
        <Route
          exact
          path="/admin/75ec639510609d/create/hospital"
          component={CreateHospital}
        />
        {/* <Route path="/add" component={AdditionalDetailsEdit} /> */}
        <Route path="/admin/dashboard" component={SuperAdminDashboard} />
        <Route exact path="/admin/update/:hospital_id" component={AdminHs} />
        <Route exact path="/ambulanceform" component={CreateForm} />
        {/* <Route exact path="/time" component={DoctorTimings} /> */}

        {/* <Route exact path="/admin/profile" component={Profile} /> */}

        {/* Redirect to homepage if route not found */}
        <Route render={() => <Redirect to="/" />} />
      </Switch>
    </LocationContext.Provider>
  );
}

export default App;
