import { useState } from 'react';
import axios from 'axios';
import { Link, useHistory } from 'react-router-dom';

const SignupForm = ({ preData }) => {
  const history = useHistory();

  const name = preData && preData?.name.split(' ');

  const [signup, setSignup] = useState({
    firstName: name?.[0] || '',
    middleName: name?.[1] || '',
    lastName: name?.[2] || '',
    mobile: preData?.phone || '',
    email: preData?.email || '',
    password: '',
    conPassword: '',
    dob: '',
    gender: '',
    placeOfResidence: '',
    aadharCardNumber: '',
    isHospitalAdmin: '',
    isRahAdmin: preData?.isRahAdmin || '',
  });
  const [profilePic, setProfilePic] = useState();
  const [status, setStatus] = useState({
    success: '',
    error: '',
  });

  const handleChange = (e) =>
    setSignup({ ...signup, [e.target.name]: e.target.value });

  const handleFileChange = (e) => {
    setProfilePic(e.target.files[0]);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const formData = new FormData();
      formData.append('profilePic', profilePic);
      Object.keys(signup).forEach(function (key) {
        formData.append(key, signup[key]);
      });
      if (signup.password === signup.conPassword) {
        if (signup.isHospitalAdmin === 'true' && signup.isRahAdmin === 'true') {
          setStatus({ error: 'Choose only one Admin' });
        } else {
          const res = await axios.post('/api/user/signup', formData, {
            headers: { 'Content-Type': 'multipart/form-data' },
          });
          if (res.data) {
            setStatus({ success: 'Signup successful' });
            if (
              res?.data?.user?.isHospitalAdmin ||
              res?.data?.user?.isRahAdmin
            ) {
              history.push('/');
            } else {
              history.push('/');
            }
          } else {
            setStatus({ error: 'Unable to create user' });
          }
        }
      } else {
        setStatus({ error: 'password did not matched' });
      }
    } catch (error) {
      setStatus({ error: error.response?.data?.error });
    }
  };

  return (
    <>
      <div className='signup'>
        <h1>{preData?.isRahAdmin ? 'RAH Admin' : 'User'} Signup</h1>
        <form onSubmit={handleSubmit}>
          <div className='row'>
            <div className='col-4'>
              <div className='input_box'>
                <label htmlFor='fname'>First name *</label>
                <input
                  type='text'
                  id='fname'
                  name='firstName'
                  value={signup.firstName}
                  onChange={handleChange}
                  required
                />
              </div>
            </div>
            <div className='col-4'>
              <div className='input_box'>
                <label htmlFor='mname'>Middle name</label>
                <input
                  type='text'
                  id='mname'
                  name='middleName'
                  value={signup.middleName}
                  onChange={handleChange}
                />
              </div>
            </div>
            <div className='col-4'>
              <div className='input_box'>
                <label htmlFor='lname'>Last name *</label>
                <input
                  type='text'
                  id='lname'
                  name='lastName'
                  value={signup.lastName}
                  onChange={handleChange}
                  required
                />
              </div>
            </div>
          </div>
          <div className='row'>
            <div className='col-6'>
              <div className='input_box'>
                <label htmlFor='mobile'>Mobile *</label>
                <input
                  type='text'
                  id='mobile'
                  name='mobile'
                  value={signup.mobile}
                  onChange={handleChange}
                  required
                />
              </div>
            </div>
            <div className='col-6'>
              <div className='input_box'>
                <label htmlFor='email'>Email *</label>
                <input
                  type='text'
                  id='email'
                  name='email'
                  value={signup.email}
                  onChange={handleChange}
                  required
                />
              </div>
            </div>
          </div>
          <div className='row'>
            <div className='col-6'>
              <div className='input_box'>
                <label htmlFor='password'>Password *</label>
                <input
                  type='text'
                  id='password'
                  name='password'
                  value={signup.password}
                  onChange={handleChange}
                  pattern='/^(?=.*\d)(?=.*[a-zA-Z])[a-zA-Z0-9]{7,}$/'
                  required
                />
              </div>
            </div>
            <div className='col-6'>
              <div className='input_box'>
                <label htmlFor='conPassword'>Confirm password *</label>
                <input
                  type='text'
                  id='conPassword'
                  name='conPassword'
                  value={signup.conPassword}
                  onChange={handleChange}
                  required
                />
              </div>
            </div>
          </div>
          <div className='row'>
            <div className='col-4'>
              <div className='input_box'>
                <label htmlFor='dob'>Date of birth </label>
                <input
                  type='date'
                  id='dob'
                  max='99'
                  name='dob'
                  value={signup.dob}
                  onChange={handleChange}
                  required
                />
              </div>
            </div>
            <div className='col-3'>
              <div className='input_box'>
                <label htmlFor='gender'>Gender</label>
                <select id='gender' name='gender' onChange={handleChange}>
                  <option value=''>Select</option>
                  <option value='male'>Male</option>
                  <option value='female'>Female</option>
                  <option value='others'>Others</option>
                </select>
              </div>
            </div>
            <div className='col-5'>
              <div className='input_box'>
                <label htmlFor='aadharCardNumber'>Aadhar card number</label>
                <input
                  type='number'
                  id='aadharCardNumber'
                  name='aadharCardNumber'
                  value={signup.aadharCardNumber}
                  onChange={handleChange}
                />
              </div>
            </div>
          </div>
          <div className='row'>
            <div className='col-12'>
              <div className='input_box'>
                <label htmlFor='placeOfResidence'>Place of residence *</label>
                <input
                  type='text'
                  id='placeOfResidence'
                  name='placeOfResidence'
                  value={signup.placeOfResidence}
                  onChange={handleChange}
                  required
                />
              </div>
            </div>
          </div>
          <div className='row'>
            {!preData?.isRahAdmin && (
              <>
                <div className='col-4'>
                  <div className='input_box'>
                    <label htmlFor='isHospitalAdmin'>Is hospital admin *</label>
                    <select
                      id='isHospitalAdmin'
                      name='isHospitalAdmin'
                      onChange={handleChange}
                      required
                    >
                      <option value=''>Select</option>
                      <option value='true'>Yes</option>
                      <option value='false'>No</option>
                    </select>
                  </div>
                </div>
                <div className='col-4'>
                  <div className='input_box'>
                    <label htmlFor='isRahAdmin'>Is RAH admin *</label>
                    <select
                      id='isRahAdmin'
                      name='isRahAdmin'
                      onChange={handleChange}
                      required
                    >
                      <option value=''>Select</option>
                      <option value='true'>Yes</option>
                      <option value='false'>No</option>
                    </select>
                  </div>
                </div>
              </>
            )}
            <div className='col-4'>
              <div className='input_box'>
                <label htmlFor='profile'>Profile picture</label>
                <input
                  type='file'
                  id='profile'
                  name='profilePic'
                  onChange={handleFileChange}
                />
              </div>
            </div>
          </div>
          <div className='input_box'>
            <button type='submit'>Signup</button>
          </div>
          {status.success && <p className='text-success'>{status.success}</p>}
          {status.error && <p className='text-danger'>{status.error}</p>}
          <div>
            Existing user? <Link to='/login'>Login</Link> &nbsp; | &nbsp;{' '}
            <Link to='/' className='btn btn-sm btn-dark'>
              Home
            </Link>
          </div>
        </form>
      </div>
    </>
  );
};

export default SignupForm;
