import { useState, useContext } from 'react';
import styled, { css } from 'styled-components';
import { BodyPartContext } from '../main.js';

const HumanSidebarLink = styled.div`
  display: flex;
  color: #363636;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  list-style: none;
  height: 60px;
  text-decoration: none;
  font-size: 18px;
  border: 1px solid #e7e7e7;
  text-decoration: none;
  width: 400px;
  border-radius: 6px;
  &:hover {
    ${'' /* background: #252831; */}
    border-left: 4px solid #632ce4;
    cursor: pointer;
    text-decoration: none;
  }
`;

const HumanSidebarLabel = styled.span`
  margin-left: 16px;
`;

const HumanDropdownLink = styled.div`
  background: #fff;
  height: 40px;
  ${'' /* padding-left: 3rem; */}
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #363636;
  font-size: 18px;
  border: 1px solid #e7e7e7;
  width: 300px;
  margin-left: 100px;
  margin-top: 5px;
  border-radius: 6px;
  &:hover {
    background: #853333;
    cursor: pointer;
    text-decoration: none;
    color: #fff;
  }
  ${({ isSelected }) =>
    isSelected &&
    css`
      color: #fff;
      background: #853333;
    `}
`;

const HumanSubMenu = ({ item }) => {
  const { selectedBodyPart, setSelectedBodyPart, handleSearchBodyPart } =
    useContext(BodyPartContext);
  const [subnav, setSubnav] = useState(false);

  const showSubnav = () => setSubnav(!subnav);

  return (
    <>
      <HumanSidebarLink onClick={item.subNav && showSubnav}>
        <div>
          {item.icon}
          <HumanSidebarLabel>{item.title}</HumanSidebarLabel>
        </div>
        <div>
          {item.subNav && subnav
            ? item.iconOpened
            : item.subNav
            ? item.iconClosed
            : null}
        </div>
      </HumanSidebarLink>
      {(subnav ||
        item.subNav.some(
          (nav) =>
            nav.title.replace(/ +/g, '').toLowerCase() ===
            selectedBodyPart.replace(/ +/g, '').toLowerCase()
        )) &&
        item.subNav.map((item, index) => {
          return (
            <HumanDropdownLink
              isSelected={
                selectedBodyPart.replace(/ +/g, '').toLowerCase() ===
                item.title.replace(/ +/g, '').toLowerCase()
              }
              onClick={() => {
                setSelectedBodyPart(item?.title);
                handleSearchBodyPart(item?.title);
              }}
              key={index}
            >
              {item.icon}
              <HumanSidebarLabel>{item.title}</HumanSidebarLabel>
            </HumanDropdownLink>
          );
        })}
    </>
  );
};

export default HumanSubMenu;
