import axios from "axios";
import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { Container, Row, Col, Button } from "react-bootstrap";
import "./createForm.css";

const CreateHospitalForm = ({ hospital_id }) => {
  console.log("--create hospital form--");
  const [isHover, setIsHover] = useState(false);
  const [loading, setLoading] = useState(false);
  const [alreadyCreated, setAlreadyCreated] = useState(false);
  const [info, setInfo] = useState({
    hospital_name: "",
    hospital_type: "",
    contact_number: "",
    hospital_email: "",
    address: "",
    about: "",
    total_seats: "",
    total_icus: "",
  });
  const [newinfo, setNewinfo] = useState({
    hospital_name: "",
    hospital_type: "",
    contact_number: "",
    hospital_email: "",
    address: "",
    about: "",
    total_seats: "",
    total_icus: "",
  });
  const [curFacility, setCurFacility] = useState({
    icon: "",
    heading: "",
    content: "",
  });
  const [facilities, setFacilities] = useState([]);
  const [curDepartment, setCurDepartment] = useState("");
  const [department, setDepartment] = useState([]);
  const [logo, setLogo] = useState();
  const [images, setImages] = useState();
  const [status, setStatus] = useState({
    success: "",
    error: "",
  });

  const [data, setData] = useState([
    "cardiology",
    "odontology",
    "gynaecology",
    "pulmology",
    "dermatology",
    "neurology",
    "paediatric",
  ]);

  const facility_icons = [
    "alarm-warning-line.png",
    "check-double-line.png",
    "search-eye-line.png",
    "git-repository-line.png",
    "history-line.png",
    "health-book-line.png",
    "hand-sanitizer-line.png",
    "Vector.png",
    "lungs-line.png",
    "scissors-line.png",
    "ruler-line.png",
    "sip-line.png",
    "truck-line.png",
    "road-map-line.png",
    "shield-line.png",
    "rest-time-line.png",
    "macbook-line.png",
    "medicine-bottle-line.png",
    "contrast-drop-2-line.png",
    "ink-bottle-line.png",
    "edit-2-line.png",
    "drop-line.png",
    "eye-line.png",
    "thumb-up-line.png",
    "heart-pulse-line.png",
    "mac-line.png",
    "mental-health-line.png",
    "store-3-line.png",
    "store-2-line.png",
    "Vector1.png",
    "Vector2.png",
    "mail-unread-line.png",
    "psychotherapy-line.png",
    "capsule-line.png",
    "Vector3.png",
    "microscope-line.png",
    "home-2-line.png",
    "flag-2-line.png",
    "Vector4.png",
    "arrow-left-right-line.png",
    "filter-line.png",
    "heart-add-line.png",
    "git-repository-private-line.png",
    "shut-down-line.png",
    "nurse-line.png",
    "Vector5.png",
    "briefcase-4-line.png",
    "inbox-unarchive-line.png",
    "message-2-line.png",
    "pulse-line.png",
    "task-line.png",
    "sticky-note-line.png",
    "smartphone-line.png",
    "hand-heart-line.png",
    "swap-line.png",
    "secure-payment-line.png",
    "walk-line.png",
    "heart-line.png",
    "surgical-mask-line.png",
    "stethoscope-line.png",
    "server-line.png",
    "gift-2-line.png",
    "vip-line.png",
    "24-hours-line.png",
    "shopping-basket-line.png",
    "todo-line.png",
    "infrared-thermometer-line.png",
    "hearts-line.png",
    "flask-line.png",
    "zzz-line.png",
    "virus-line.png",
    "thermometer-line.png",
    "test-tube-line.png",
    "syringe-line.png",
    "dossier-line.png",
    "empathize-line.png",
    "first-aid-kit-line.png",
  ];

  const [previewLogo, setPreviewLogo] = useState(null);
  const [previewImage, setPreviewImage] = useState([]);

  const handleInfoChange = (e) => {
    setNewinfo({ ...newinfo, [e.target.name]: e.target.value });
    setInfo({ ...info, [e.target.name]: e.target.value });
  };

  const handleLogoChange = (e) => {
    const file = e.target.files[0];
    setLogo(file);
    file && setPreviewLogo(URL.createObjectURL(file));
  };

  let mFileObj = [];
  let mFileArray = [];
  const uploadMultipleFiles = (mfiles) => {
    mFileObj.push(mfiles);
    for (let i = 0; i < mFileObj[0].length; i++) {
      mFileArray.push(URL.createObjectURL(mFileObj[0][i]));
    }
    setPreviewImage(mFileArray);
  };
  const handleImageChange = (e) => {
    const files = e.target.files;
    setImages(files);
    uploadMultipleFiles(files);
  };

  const handleFacilityChange = (e) => {
    setCurFacility({ ...curFacility, [e.target.name]: e.target.value });
  };
  // const handleIconClick = (icon) => {
  //   //setCurFacility({ ...curFacility, icon });
  //   setIsHover(false);
  // };

  const handleDeptChange = (e) => setCurDepartment(e.target.value);
  
  
  const handleAddDept = () => {
    if (curDepartment && !department.includes(curDepartment)) {
      setDepartment([...department, curDepartment]);
      setCurDepartment("");
   
    } else {
      alert("Empty or department already exists");
    }
  };
  
  const handleRemoveDept = (item) => {
    setDepartment(department.filter((dept) => dept !== item));
  };

  const handleDeptEdit = (indx) => {
    setCurDepartment(department[indx]);
  };

  

  const handleAddFacility = () => {
    if (curFacility.heading !== "" && curFacility.content !== "") {
      setFacilities([...facilities, curFacility]);
      setCurFacility({ icon: "", heading: "", content: "" });
    }
  };

  const handleEditFacility = (indx) => {
    const getFacility = facilities[indx];
    setCurFacility({ ...getFacility });
  };

  const handleRemoveFacility = (indx) => {
    const new_facilities = [...facilities];
    new_facilities.splice(indx, 1);
    setFacilities(new_facilities);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      // setLoading(true);
      // setStatus({ success: '', error: '' });
      const formData = new FormData();

      for (const key of Object.keys(info)) {
        formData.append(key, info[key]);
      }
      if (logo) formData.append("logo", logo);
      if (images) {
        for (const key of Object.keys(images)) {
          formData.append("images", images[key]);
        }
      }
      // JSON.stringify(department)
      if (!!department.length) {
        const departmentList = department;
        newinfo["department"] =  department ;
        console.log("this is department", department);
        formData.append("department", departmentList);
      }

      if (facilities.length) {
        const facilitiesList = facilities;
        console.log("this is facility", facilities);
        newinfo["facilities"] =  facilities ;
        formData.append("facilities", facilitiesList);
      }
      // } else {
      //   setLoading(false);
      //   setStatus({ success: '', error: 'Add minimum 4 facilities' });
      //   return;
      // }

      if (!!hospital_id || alreadyCreated) {
        console.log("hi updated", hospital_id);
        console.log("this is :", newinfo);
        const res = await axios.put(
          `http://rah-model.coeaibbsr.in/api/hospital/${hospital_id}`,newinfo
          // {
          //   department: [],
          //   facilities: [],
          // }
          
          // JSON.stringify(newinfo)
          
          
          //{ hospital_name: "PADMA HOSPITAL", hospital_type: "Private" }
          // {
          //   about: "",
          //   address: "",
          //   contact_number: "",
          //   department: ["ENT"],
          //   hospital_email: "",
          //   hospital_name: "PADMA HOSPITAL",
          //   hospital_type: "Private",

          //   total_icus: "10",
          //   total_seats: "10",
          // }
        );
        console.log(res)
        if (res?.statusText) {
          setLoading(false);
          setStatus({ success: "Hospital data updated" });
        } else {
          setLoading(false);
          setStatus({ error: "Unable to insert data" });
        }
      } else {
        const res = await axios.post("/api/hospital/create", formData);

        if (res?.statusText) {
          setLoading(false);
          setStatus({ success: "Hospital data inserted" });
          setAlreadyCreated(true);
        } else {
          setLoading(false);
          setStatus({ error: "Unable to insert data" });
        }
      }
    } catch (error) {
      setLoading(false);
      setStatus({ error: error.response?.data?.error });
    }
  };

  useEffect(() => {
    !!hospital_id &&
      axios.get(`/api/hospital/details/${hospital_id}`).then((res) => {
        const hospital = res.data?.hospital;
        if (!!hospital) {
          setAlreadyCreated(true);
          let newhospital = {
            ...hospital,
          };
          delete newhospital["createdAt"];
          delete newhospital["reviews"];
          delete newhospital["__v"];
          delete newhospital["images"];
          delete newhospital["logo"];
          setNewinfo({ ...newhospital });
          setInfo({ ...hospital });
          setLogo(hospital?.logo);
          setPreviewLogo(hospital?.logo);
          setImages(hospital?.images);
          setPreviewImage(hospital?.images);
          setDepartment(hospital?.department.filter((dept) => !!dept));
          setFacilities(hospital?.facilities);
        }
      });
    return;
  }, [hospital_id]);

  return (
    <>
      <div className="form_container create_hospital">
        <h1>Create Hospial Form</h1>
        <form onSubmit={handleSubmit}>
          <div className="row">
            <div className="col-md-4 col-xs-12">
              <div className="input_box">
                <label htmlFor="name">Hospital Name *</label>
                <input
                  type="text"
                  id="name"
                  name="hospital_name"
                  value={info.hospital_name}
                  onChange={handleInfoChange}
                  // required
                />
              </div>
            </div>
            <div className="col-md-3 col-xs-6">
              <div className="input_box">
                <label htmlFor="type">Hospital Type *</label>
                <select
                  id="type"
                  name="hospital_type"
                  onChange={handleInfoChange}
                  // required
                >
                  <option value="">Select</option>
                  <option
                    value="government"
                    selected={
                      info?.hospital_type.toLowerCase() === "government"
                    }
                  >
                    Government
                  </option>
                  <option
                    value="private"
                    selected={info?.hospital_type.toLowerCase() === "private"}
                  >
                    Private
                  </option>
                </select>
              </div>
            </div>
            <div className="col-md-3 col-xs-6">
              <div className="input_box">
                <label htmlFor="logo">Logo *</label>
                <input
                  type="file"
                  name="logo"
                  onChange={handleLogoChange}
                  accept="image/*"
                  // required
                />
              </div>
            </div>
            <div className="col-md-2 col-xs-6">
              <button
                style={{
                  height: "35px",
                  marginTop: "35px",
                  background: "black",
                  color: "white",
                }}
              >
                Submit
              </button>
            </div>
            <div className="col-2">
              {previewLogo && (
                <img
                  src={previewLogo}
                  style={{
                    height: "78px",
                    width: "90px",
                    objectFit: "contain",
                  }}
                />
              )}
            </div>
          </div>
          <div className="row">
            <div className="col-md-3 col-xs-6">
              <div className="input_box">
                <label htmlFor="contact">Contact number *</label>
                <input
                  type="text"
                  id="contact"
                  name="contact_number"
                  // pattern='^(\+91[\-\s]?)?[0]?(91)?[789]\d{9}$'
                  title="Invalid contact number"
                  minLength="10"
                  maxLength="10"
                  value={info.contact_number}
                  onChange={handleInfoChange}
                  // required
                />
              </div>
            </div>
            <div className="col-md-4 col-xs-6">
              <div className="input_box">
                <label htmlFor="email">Email *</label>
                <input
                  type="text"
                  id="email"
                  name="hospital_email"
                  pattern="^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$"
                  title="Invalid email"
                  value={info.hospital_email}
                  onChange={handleInfoChange}
                  // required
                />
              </div>
            </div>
            <div className="col-md-5 col-xs-6">
              <div className="input_box">
                <label htmlFor="address">Address *</label>
                <input
                  type="text"
                  id="address"
                  name="address"
                  value={info.address}
                  onChange={handleInfoChange}
                  // required
                />
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-md-3 col-xs-6">
              <div className="input_box">
                <label htmlFor="total_seats">Total beds *</label>
                <input
                  type="number"
                  id="total_seats"
                  name="total_seats"
                  value={info.total_seats}
                  onChange={handleInfoChange}
                  // required
                />
              </div>
            </div>
            <div className="col-md-3 col-xs-6">
              <div className="input_box">
                <label htmlFor="total_icus">Total ICUs *</label>
                <input
                  type="number"
                  id="total_icus"
                  name="total_icus"
                  value={info.total_icus}
                  onChange={handleInfoChange}
                  // required
                />
              </div>
            </div>
            <div className="col-md-3 col-xs-6">
              <div className="input_box">
                <label htmlFor="image">Image</label>
                <input
                  type="file"
                  id="image"
                  name="image"
                  onChange={handleImageChange}
                  accept="image/*"
                  multiple
                  // required
                />
              </div>
            </div>
            <div className="col-md-3 col-xs-6">
              <button
                style={{
                  height: "35px",
                  marginTop: "35px",
                  background: "black",
                  color: "white",
                }}
              >
                Submit
              </button>
            </div>
          </div>
          <div className="pb-3">
            {previewImage &&
              previewImage.map((img, indx) => (
                <img
                  key={indx}
                  src={img}
                  onClick={(e) => {
                    setPreviewImage((prevState) =>
                      prevState.filter((item) => item !== img)
                    );
                    let copyImgs = [...images];
                    copyImgs.splice(indx, 1);
                    setImages(copyImgs);
                  }}
                  style={{
                    height: "100px",
                    width: "100px",
                    objectFit: "contain",
                    margin: "0 5px",
                  }}
                />
              ))}
          </div>
          <div className="col-12">
            <h2>About</h2>
            <small>{500 - info.about.length} characters left</small>
            <textarea
              className="about"
              name="about"
              maxLength="500"
              defaultValue={info?.about}
              onChange={handleInfoChange}
              // required
            ></textarea>
          </div>
          <h2>Departments *</h2>
          <div className="col-12">
            <div className="d-flex justify-content-start align-items-center">
              <input
                type="text"
                name="curDepartment"
                value={curDepartment}
                onChange={handleDeptChange}
              />
              <button
                type="button"
                onClick={handleAddDept}
                className="btn btn-dark text-nowrap ms-2"
              >
                Add +
              </button>
            </div>
            <div className="rept_res">
              {department &&
                !!department.length &&
                department.map((dept, indx) => (
                  <div
                    className="dept"
                    key={indx}
                    onClick={() => handleDeptEdit(indx)}
                  >
                    <h4 onClick={() => handleRemoveDept(dept)}>&times;</h4>
                    <p>{dept}</p>
                  </div>
                ))}
            </div>
          </div>

          {/* <div className='d-flex flex-wrap'>
            {data.map((dept, indx) => (
              <div key={indx}>
                <label
                  htmlFor={indx}
                  className='text-capitalize border border-primary px-2 py-1 mb-2 me-3'
                >
                  <input
                    onChange={(e) => {
                      // add to list
                      if (e.target.checked) {
                        setDepartment([...department, dept]);
                      } else {
                        // remove from list
                        setDepartment(
                          department.filter((Dept) => Dept !== dept)
                        );
                      }
                    }}
                    type='checkbox'
                    id={indx}
                    value={department}
                    checked={department.some((Dept) => Dept === dept)}
                  />
                  {dept}
                </label>
              </div>
            ))}
          </div> */}
          <br />
          <h2>Facilities *</h2>
          <div className="input_box">
            <div className="row pb-2">
              <div className="col-4">
                <input
                  type="text"
                  name="heading"
                  value={curFacility.heading}
                  onChange={handleFacilityChange}
                  placeholder="Heading"
                />
              </div>
              <div className="col-4">
                <input
                  type="text"
                  name="content"
                  onChange={handleFacilityChange}
                  value={curFacility.content}
                  placeholder="Description"
                />
              </div>
            </div>
            <div className="col-11 pt-2 pb-2 icon_box">
              <Button
                onClick={() => setIsHover(!isHover)}
                variant="outline-primary"
              >
                {!!curFacility?.icon ? (
                  <img
                    className="icons_show"
                    src={`${process.env.PUBLIC_URL}/images/facilities/${curFacility?.icon}`}
                    alt="icon1"
                  />
                ) : (
                  "Icon"
                )}
              </Button>
              {isHover && (
                <div className="icon_content">
                  <Container fluid>
                    <Row>
                      {facility_icons.map((icon, indx) => (
                        <Col
                          xs={3}
                          md={1}
                          key={indx}
                          //onClick={() => handleIconClick(icon)}
                        >
                          <img
                            className="icons_show"
                            src={`${process.env.PUBLIC_URL}/images/facilities/${icon}`}
                            alt="icon1"
                          />
                        </Col>
                      ))}
                    </Row>
                  </Container>
                </div>
              )}
            </div>
            <div className="d-flex pb-2">
              <button
                type="button"
                className="add_btn btn-dark"
                onClick={handleAddFacility}
              >
                Add +
              </button>
              <button
                type="button"
                className="add_btn btn-secondary"
                onClick={() =>
                  setCurFacility({ icon: "", heading: "", content: "" })
                }
              >
                Clear Fields
              </button>
            </div>
            <div className="row">
              <div className="facility_results">
                {facilities &&
                  !!facilities.length &&
                  facilities.map((fas, indx) => (
                    <div
                      className="fas_res"
                      key={indx}
                      onClick={() => handleEditFacility(indx)}
                    >
                      <div
                        onClick={() => handleRemoveFacility(indx)}
                        className="res_close"
                      >
                        &times;
                      </div>
                      <div>
                        <img
                          src={`${process.env.PUBLIC_URL}/images/facilities/${fas?.icon}`}
                          alt=""
                        />
                      </div>
                      <p>{fas?.heading}</p>
                      <p>{fas?.content}</p>
                    </div>
                  ))}
              </div>
            </div>
          </div>
          <div>
            {hospital_id || alreadyCreated ? (
              <button type="submit">Update</button>
            ) : (
              <button type="submit">Submit</button>
            )}
          </div>
          {loading && <p className="text-info">Loading...</p>}
          {status.success && <p className="text-success">{status.success}</p>}
          {status.error && <p className="text-danger">{status.error}</p>}
          <div>
            <Link to="/" className="btn btn-sm btn-dark">
              Go to Homepage
            </Link>
          </div>
        </form>
      </div>
    </>
  );
};

export default CreateHospitalForm;
