import React from "react";
import "../styles/table.css";
import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";
import paginationFactory from "react-bootstrap-table2-paginator";
import DirectionsOutlinedIcon from "@mui/icons-material/DirectionsOutlined";
import BootstrapTable from "react-bootstrap-table-next";

function Table({ isCoords, hospitals }) {
  function priceFormatter(cell, row) {
    const showInMapClicked = (address) => {
      isCoords &&
        window.open(
          `https://www.google.com/maps/dir/?api=1&destination=${address}`
        );
    };

    return (
      <div className="column-img">
        <div className="righter-img-phone">
          <a className="text active" href={`tel:${row?.contact_number}`}>
            <img src={require("../img/PhoneCall.png").default} alt="" />
          </a>
        </div>
        <div
          className="righter-img-mail"
          onClick={() => showInMapClicked(row?.address)}
        >
          <DirectionsOutlinedIcon />
        </div>
      </div>
    );
  }

  let columns = [];

  if (isCoords) {
    columns = [
      {
        dataField: "hospital_name",
        text: "organization name",
        headerStyle: {
          background: "#E5E5E5",
          font: "Inter",
          fontStyle: "normal",
          fontWeight: " 600",
          fontSize: "14px",

          color: "#949494",
        },
      },
      {
        dataField: "hospital_type",
        text: "category",
        headerStyle: {
          background: "#E5E5E5",
          font: "Inter",
          fontStyle: "normal",
          fontWeight: " 600",
          fontSize: "14px",

          color: "#949494",
        },
      },
      {
        dataField: "distance",
        text: "Distance radius",
        headerStyle: {
          background: "#E5E5E5",
          font: "Inter",
          fontStyle: "normal",
          fontWeight: " 600",
          fontSize: "14px",

          color: "#949494",
        },
      },
      {
        dataField: "contact_number",
        text: "Ambulance Number",
        headerStyle: {
          background: "#E5E5E5",
          font: "Inter",
          fontStyle: "normal",
          fontWeight: " 600",
          fontSize: "14px",

          color: "#949494",
        },
      },
      {
        dataField: "price",
        text: "Action",
        formatter: priceFormatter,
        headerStyle: {
          background: "#E5E5E5",
          font: "Inter",
          fontStyle: "normal",
          fontWeight: " 600",
          fontSize: "14px",
          color: "#949494",
        },
      },
    ];
  } else {
    columns = [
      {
        dataField: "hospital_name",
        text: "organization name",
      },
      {
        dataField: "hospital_type",
        text: "category",
      },
      {
        dataField: "contact_number",
        text: "Ambulance Number",
      },
      {
        dataField: "price",
        // text: "action",
        text: "",
        formatter: priceFormatter,
      },
    ];
  }

  const rowStyle = (row, rowIndex) => {
    return rowIndex % 2 === 0
      ? { backgroundColor: "#fff4f4" }
      : { backgroundColor: "white" };
  };
  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      Showing {from} to {to} of {size} Results
    </span>
  );
  const options = {
    paginationSize: 4,
    pageStartIndex: 1,
    // alwaysShowAllBtns: true, // Always show next and previous button
    // withFirstAndLast: false, // Hide the going to First and Last page button
    // hideSizePerPage: true, // Hide the sizePerPage dropdown always
    // hidePageListOnlyOnePage: true, // Hide the pagination list when only one page
    firstPageText: "First",
    prePageText: "Back",
    nextPageText: "Next",
    lastPageText: "Last",
    nextPageTitle: "First page",
    prePageTitle: "Pre page",
    firstPageTitle: "Next page",
    lastPageTitle: "Last page",
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [5],
  };
  return (
    <>
      <div className="boot-table">
        <BootstrapTable
          keyField="_id"
          data={hospitals}
          columns={columns}
          rowStyle={rowStyle}
          bordered={false}
          pagination={paginationFactory(options)}
        />
      </div>
    </>
  );
}
export default Table;
