import React from "react";
import { Table, Container, Row, Col, Button } from "react-bootstrap";
export default function FeatureDetails({ items }) {
  return (
    <>
      <Table striped>
        <thead>
          <tr>
            <th>Sl. No.</th>
            <th>Facilities</th>
            <th>Icon</th>
            <th>Description</th>
          </tr>
        </thead>
        <tbody>
          {!!items &&
            items.length &&
            items.map((item, indx) => (
              <tr key={indx}>
                <td>{indx + 1}</td>
                <td>{item?.heading}</td>
                <td>
                  <img
                    src={`${process.env.PUBLIC_URL}/images/facilities/${item?.icon}`}
                    alt=""
                  />
                </td>
                <td>{item?.content}</td>
              </tr>
            ))}
        </tbody>
      </Table>
    </>
  );
}
