import { useState, useEffect, useContext } from "react";
import { useParams, useHistory } from "react-router-dom";
// import NavBar from '../../hospitalPage/components/hospital';
import SearchTop from "./SearchTop";
import Footer from "../../landingPage/components/footer";
import Appointment from "../../hospitalPage/components/apppointment";
import "../styles/Search.css";
import SearchResult from "./SearchResult";
import { LocationContext } from "../../App";
import axios from "axios";
import NavBar from "../../NavigationBar/Navbar";
import { Helmet } from "react-helmet";
import Preloader from "../../Preloader";
import Emergency_call from "../../landingPage/components/emergency_call";
import FooterCredit from "../../Footer_Credit/FooterCredit";

function Search() {
  const history = useHistory();
  const searchHospitalsFromHome = history.location?.state?.list;
  const searchFieldsFromHome = history.location?.state?.searchField;
  const { address, location } = useContext(LocationContext);
  const { category, item } = useParams();

  const [preloading, setPreloading] = useState(true);
  const [loading, setLoading] = useState(true);
  const [searchedItems, setSearchedItems] = useState([]);

  const handleSetSearchedItems = (items) => {
    items?.list.sort((a, b) => a?.distance - b?.distance);
    setSearchedItems(items?.list);
  };

  useEffect(() => {
    // Get all hospitals
    !searchHospitalsFromHome &&
      axios
        .get("/api/hospital/all?page=1", {
          headers: {
            location: JSON.stringify(location),
          },
        })
        .then((res) => {
          console.log(res.data);
          if (res.data?.hospital) {
            setLoading(false);
            setPreloading(false);
            const radius = res.data?.hospital.filter((i) => i?.distance < 100);
            radius.sort((a, b) => a?.distance - b?.distance);
            setSearchedItems(radius);
          }
        })
        .catch((err) => console.log(err.response));
  }, [location]);

  useEffect(() => {
    //   Get searched item if needed
    if (category === "department") {
      axios
        .get(`/api/hospital/dept/${item}`, {
          headers: {
            location: JSON.stringify(location),
          },
        })
        .then((res) => {
          if (res.data?.hospital) {
            setLoading(false);
            setPreloading(false);
            res.data?.hospital.sort((a, b) => a?.distance - b?.distance);
            setSearchedItems(res.data?.hospitals);
          }
        })
        .catch((err) => console.log(err.response));
    }
  }, [item]);

  useEffect(() => {
    // Set searchItem returned from home
    if (searchHospitalsFromHome) {
      searchHospitalsFromHome.sort((a, b) => a?.distance - b?.distance);
      setSearchedItems(searchHospitalsFromHome);
      setLoading(false);
      setPreloading(false);
    }
  }, []);

  return (
    <div className="Search-page">
      <Helmet>
        <title>Search Page</title>
      </Helmet>
      {preloading ? (
        <Preloader />
      ) : (
        <>
          <Emergency_call />
          <NavBar />
          <hr className="nav-line" />
          <SearchTop
            setLoading={setLoading}
            address={address}
            searchFieldsFromHome={searchFieldsFromHome}
            handleSetSearchedItems={handleSetSearchedItems}
          />
          <SearchResult
            loading={loading}
            hospitals={searchedItems}
            setHospitals={setSearchedItems}
          />
          <div className="top-color-nothing"></div>
          <Appointment />
          <br />
          <br />
          <Footer />
          <FooterCredit />
        </>
      )}
    </div>
  );
}

export default Search;
