import React from 'react';
import './Uploader.css';
import ImageUploading from 'react-images-uploading';

export default function Uploader({ logo }) {
  const [images, setImages] = React.useState([]);
  const maxNumber = 1;

  const onChange = (imageList, addUpdateIndex) => {
    // data for submit
    console.log(imageList, addUpdateIndex);
    setImages(imageList);
    console.log(images);
  };

  React.useEffect(() => setImages(logo), [logo]);

  return (
    <>
      <div className='SingleImageUploader-Main'>
        <div className='uploader-body'>
          <ImageUploading
            isDragging={true}
            value={images}
            onChange={onChange}
            onDrop={onChange}
            maxNumber={maxNumber}
            dataURLKey='data_url'
          >
            {({
              imageList,
              onImageUpload,
              onImageRemove,
              isDragging,
              dragProps,
            }) => (
              <>
                <img
                  src={require('../../img/Vector (2).png').default}
                  style={isDragging ? { color: 'red' } : undefined}
                  onClick={onImageUpload}
                  {...dragProps}
                  alt='hi'
                />
                <div className='Image-List'>
                  {/* {imageList &&
                    imageList.map((image, index) => (
                      <div key={index} className='image-item'>
                        <img src={image['data_url']} alt='' width='100' />
                        <div className='image-item__btn-wrapper'>
                          <button onClick={() => onImageRemove(index)}>
                            X
                          </button>
                        </div>
                      </div>
                    ))} */}
                  {logo && (
                    <div className='image-item'>
                      <img src={logo} alt='' width='100' />
                      <div className='image-item__btn-wrapper'>
                        <button onClick={() => onImageRemove(logo)}>X</button>
                      </div>
                    </div>
                  )}
                </div>
              </>
            )}
          </ImageUploading>
        </div>
      </div>
    </>
  );
}
