import React, { useEffect } from 'react';
import { useState } from 'react';
import ImageUploading from 'react-images-uploading';
import './Multiuploader.css';
import Modal from 'react-modal';
import MoreImage from '../MoreImage';

export default function MultiUploader({ images: imgs }) {
  // console.log(imgs);
  const [isModalOpen, setModalOpen] = useState(false);
  const [images, setImages] = React.useState([]);
  const maxNumber = 20;

  const onChange = (imageList, addUpdateIndex) => {
    // data for submit
    console.log(imageList, addUpdateIndex);
    setImages(imageList);
  };

  useEffect(() => setImages(imgs), [imgs]);

  return (
    <div className='MultipleImageUploader-main'>
      <ImageUploading
        multiple
        isDragging={true}
        value={images}
        onChange={onChange}
        onDrop={onChange}
        maxNumber={maxNumber}
        dataURLKey='data_url'
      >
        {({
          imageList,
          onImageUpload,
          onImageRemoveAll,
          onImageUpdate,
          onImageRemove,
          isDragging,
          dragProps,
        }) => (
          // write your building UI
          <div className='upload__image-wrapper'>
            <div
              className='multi-uploader'
              style={isDragging ? { color: 'red' } : undefined}
              onClick={onImageUpload}
              {...dragProps}
            >
              <div className='leftpart'>
                {' '}
                <img src={require('../../img/Group.png').default} />
              </div>
              <div className='rightpart'>
                Drag your images here or{' '}
                <span style={{ color: '#00ACB2' }}>Browse</span>{' '}
              </div>
            </div>
            <div className='Image-List'>
              {imageList.length <= 6 ? (
                imageList.map((image, index) => (
                  <div key={index} className='image-item'>
                    {console.log({ image })}
                    <img src={image} alt='' width='100' />
                    <div className='image-item__btn-wrapper'>
                      <button
                        className='remove-button'
                        onClick={() => onImageRemove(index)}
                      >
                        X
                      </button>
                    </div>
                  </div>
                ))
              ) : (
                <>
                  {imageList.slice(0, 5).map((image, index) => (
                    <div key={index} className='image-item'>
                      <img src={image} alt='' width='100' />
                      <div className='image-item__btn-wrapper'>
                        <button
                          className='remove-button'
                          onClick={() => onImageRemove(index)}
                        >
                          X
                        </button>
                      </div>
                    </div>
                  ))}
                  <div className='image-item'>
                    <img src={imageList[6]} alt='' width='100' />
                    <div className='image-item__btn-wrapper'>
                      <button
                        className='remove-button show-more-button'
                        onClick={() => {
                          setModalOpen(true);
                        }}
                      >
                        +{imageList.length - 6}more images
                      </button>
                    </div>
                  </div>
                </>
              )}
            </div>
          </div>
        )}
      </ImageUploading>
      <Modal
        className='Image-Modal'
        isOpen={isModalOpen}
        onRequestClose={() => setModalOpen(false)}
        style={{
          overlay: {
            backgroundColor: 'rgba(0,0,0,0.5)',
            width: '100%',
            display: 'flex',
          },
          content: {
            border: 'none',
            background: 'transparent',
            width: '75%',
            margin: 'auto',
            marginLeft: '20%',
            height: '550px',
          },
        }}
      >
        <div className='ImageModal-Main'>
          <div className='ImageModal-Body'>
            <MoreImage images={images} />
          </div>
        </div>
      </Modal>
    </div>
  );
}
