import { useState, useEffect, useContext, useLayoutEffect } from 'react';
import '../styles/SearchResult.css';
import SearchCard from './SearchCard';
import FilterAltOutlinedIcon from '@mui/icons-material/FilterAltOutlined';
import ImportExportOutlinedIcon from '@mui/icons-material/ImportExportOutlined';
import { LocationContext } from '../../App';
import axios from 'axios';
import GoogleMapReact from 'google-map-react';
import { Marker, Popup } from './Marker';
import { GOOGLE_MAP_API_KEY } from '../../env';

function SearchResult({ loading, hospitals }) {
  const [allHospitals, setAllHospitals] = useState(hospitals);
  const [searchFilter, setSearchFilter] = useState('');
  const [selectedHospital, setSelectedHospital] = useState(null);
  const { isCoords, location } = useContext(LocationContext);
  const [departments, setDepartments] = useState([
    'cardiology',
    'neurology',
    'orthopedic',
    'pulmonology',
    'oncology',
  ]);
  const [origDepartments, setOrigDepartments] = useState([]);
  const [selectedLoc, setSelectedLoc] = useState(null);
  const [defaultProps, setDefaultProps] = useState({
    center: {
      lat: location?.[0],
      lng: location?.[1],
    },
    zoom: 11,
  });

  useLayoutEffect(() => setAllHospitals(hospitals), [hospitals]);

  const handleGetHospital = (id) => {
    const getSingleHospital =
      selectedHospital?._id === id
        ? null
        : allHospitals.find((i) => i._id === id);
    setSelectedHospital(getSingleHospital);
  };

  const handleSearchFilter = (e) => {
    setSearchFilter(e.target.value.trim());
    setDepartments(() =>
      e.target.value === ''
        ? origDepartments
        : origDepartments.filter((dept) =>
            dept
              .toLowerCase()
              .trim()
              .includes(e.target.value.toLowerCase().trim())
          )
    );
  };

  const handleDeptFilter = (deptName) => {
    const filterByDept =
      deptName === 'all'
        ? hospitals
        : hospitals.filter((hospital) =>
            hospital?.department
              .map((dept) => dept.trim().toLowerCase())
              .includes(deptName.toLowerCase().trim())
          );
    setAllHospitals(filterByDept);
  };

  const sortFunction = (action) => {
    if (action === 'rating') {
      let sortedItems = allHospitals.sort((a, b) => a?.rating - b?.rating);
      setAllHospitals([...sortedItems]);
    }
    if (action === 'distance') {
      let sortedItems = allHospitals.sort((a, b) => a?.distance - b?.distance);
      setAllHospitals([...sortedItems]);
    }
    if (action === 'alphabetical') {
      let sortedItems = allHospitals.sort((a, b) =>
        a.hospital_name > b.hospital_name
          ? 1
          : b.hospital_name > a.hospital_name
          ? -1
          : 0
      );
      setAllHospitals([...sortedItems]);
    }
  };

  useEffect(() => {
    console.log(allHospitals?.[0]?.location?.coordinates?.[1]);
    setDefaultProps({
      ...defaultProps,
      center: {
        lat: allHospitals?.[0]?.location?.coordinates?.[1],
        lng: allHospitals?.[0]?.location?.coordinates?.[0],
      },
    });

    return;
  }, [location, allHospitals?.[0]?.location?.coordinates?.[1]]);

  // useEffect(() => {
  //   !departments.length &&
  //     axios
  //       .get('/api/hospital/departments')
  //       .then((res) => {
  //         setDepartments(res.data);
  //         setOrigDepartments(res.data);
  //       })
  //       .catch((err) => console.log(err.response));
  // }, []);

  return (
    <div className='SearchResult'>
      {loading ? (
        <div className='loading_section'>
          <h4>Loading...</h4>
        </div>
      ) : (
        <div className='body'>
          <div className='left-1'>
            <div className='head'>
              <div class='dropdown-filter'>
                <div className='but-1'>
                  Filter <FilterAltOutlinedIcon className='but-1-icon' />
                </div>
                <div class='dropdown-content'>
                  <div className='search_field'>
                    {/* <input
                      type='text'
                      value={searchFilter}
                      onChange={handleSearchFilter}
                      placeholder='e.g: cardiology'
                    /> */}
                  </div>
                  <div
                    onClick={() => handleDeptFilter('all')}
                    className='filterLink'
                  >
                    All
                  </div>
                  {departments &&
                    !!departments.length &&
                    departments.map((dept, indx) => (
                      <div
                        onClick={() => handleDeptFilter(dept)}
                        className='filterLink text-capitalize'
                        key={indx}
                      >
                        {dept}
                      </div>
                    ))}
                </div>
              </div>

              <div class='dropdown-filter'>
                <div className='but-1'>
                  Sort <ImportExportOutlinedIcon className='but-1-icon' />
                </div>
                <div class='dropdown-content'>
                  <a onClick={() => sortFunction('rating')}>Rating</a>
                  <a onClick={() => sortFunction('distance')}>Distance</a>
                  <a onClick={() => sortFunction('alphabetical')}>A-Z</a>
                </div>
              </div>
            </div>
            <div className='tail'>
              {allHospitals &&
              Array.isArray(allHospitals) &&
              !!allHospitals.length ? (
                allHospitals
                  // .slice(0, 50)
                  .map((item) => (
                    <>
                    {item?.distance <=100?
                      <SearchCard
                      key={item?._id}
                      id={item?._id}
                      handleGetHospital={handleGetHospital}
                      name={item.hospital_name}
                      type={item?.hospital_type}
                      img={item?.logo}
                      contact_number={item?.contact_number}
                      isCoords={isCoords}
                      distance={item?.distance}
                      address={item?.address}
                      rating={item?.rating}
                      location={item?.location}
                      setSelectedLoc={setSelectedLoc}
                      selectedHospital={selectedHospital}
                    />:null}
                  
                    </>
                  ))
              ) : (
                <h4 className='text-capitalize text-center text-danger'>
                  No hospital found
                </h4>
              )}
            </div>
          </div>
          <div className='right'>
            <div className='map_area'>
              <GoogleMapReact
                bootstrapURLKeys={{
                  key: GOOGLE_MAP_API_KEY,
                }}
                defaultCenter={defaultProps.center}
                defaultZoom={defaultProps.zoom}
              >
                {allHospitals &&
                  !selectedHospital &&
                  Array.isArray(allHospitals) &&
                  !!allHospitals.length &&
                  allHospitals
                    // .slice(0, 50)
                    .map(
                      (hospital, indx) =>
                        !!hospital?.location?.coordinates && (
                          <Marker
                            key={indx}
                            lat={hospital?.location?.coordinates?.[1]}
                            lng={hospital?.location?.coordinates?.[0]}
                            setSelectedLoc={setSelectedLoc}
                            hospital={hospital}
                          />
                        )
                    )}
                {location && (
                  <Marker
                    lat={location?.[0]}
                    lng={location?.[1]}
                    setSelectedLoc={setSelectedLoc}
                    hospital={selectedHospital}
                    isActive={true}
                  />
                )}
                {selectedHospital &&
                  !!selectedHospital?.location?.coordinates && (
                    <Marker
                      lat={selectedHospital?.location?.coordinates?.[1]}
                      lng={selectedHospital?.location?.coordinates?.[0]}
                      setSelectedLoc={setSelectedLoc}
                      hospital={selectedHospital}
                    />
                  )}
                {selectedLoc && (
                  <Popup
                    lat={selectedLoc?.location?.coordinates?.[1]}
                    lng={selectedLoc?.location?.coordinates?.[0]}
                    name={selectedLoc?.hospital_name}
                    distance={selectedLoc?.distance}
                    address={selectedLoc?.address}
                    contact_number={selectedLoc?.contact_number}
                    setSelectedLoc={setSelectedLoc}
                  />
                )}
              </GoogleMapReact>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default SearchResult;
