import React from "react";
import "../styles/doctor.css";
import { Link } from "react-router-dom";
import Slider from "react-slick";
function Doctor() {
  const settings = {
    centerPadding: "60px",
    dots: true,
    infinite: true,
    speed: 500,
    arrow: false,
    slidesToShow: 6,
    slidesToScroll: 4,

    responsive: [
      {
        breakpoint: 1344,
        settings: {
          slidesToShow: 5,
          slidesToScroll: 3,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 1124,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 3,
          infinite: true,
          dots: true,
        },
      },

      {
        breakpoint: 800,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  return (
    <>
      <div className="doctor">
        <div className="body">
          <div className="heading-body">
            <img
              className="heading-background"
              src={require("../img/Vector.png").default}
            />
            <h3 className="heading">Choose your department</h3>
          </div>

          {/* <div className='line-design' >
            <img
              src={require('../img/shape/section-title-line.png').default}
              alt=''
            />
          </div> */}

          {/* <div className="parent-line">
              <div className="line-1"></div>
              <div className="line-2"></div>
            </div> */}
          <Slider className="LandingPageDepartment" {...settings}>
            <Link to="/search/department/cardiology">
              <img
                src={require("../img/rah-assets/Cardiology.png").default}
                alt=""
                id="cardiology"
                className="doct-slider-img"
              />
            </Link>
            <Link to="/search/department/odontology">
              <img
                src={require("../img/rah-assets/Odontology.png").default}
                alt=""
                id="odontology"
                className="doct-slider-img"
              />
            </Link>
            <Link to="/search/department/gynaecology">
              <img
                src={require("../img/rah-assets/Gynaecology.png").default}
                alt=""
                id="gynaecology"
                className="doct-slider-img"
              />
            </Link>
            <Link to="/search/department/pulmology">
              <img
                src={require("../img/rah-assets/Pulmology.png").default}
                alt=""
                id="pulmology"
                className="doct-slider-img"
              />
            </Link>
            <Link to="/search/department/pedistric">
              <img
                src={require("../img/rah-assets/Pedistric.png").default}
                alt=""
                id="pedistric"
                className="doct-slider-img"
              />
            </Link>

            <Link to="/search/department/dermatology">
              <img
                src={require("../img/rah-assets/Dermatology.png").default}
                alt=""
                id="dermatology"
                className="doct-slider-img"
              />
            </Link>
          </Slider>
        </div>
      </div>
    </>
  );
}

export default Doctor;
