import { ArrowBack, InfoOutlined } from '@material-ui/icons';
import './Page.css';

const ThirdPage = ({
  signup,
  handleChange,
  setStatus,
  handleSubmit,
  setActiveStep,
  setFirstStepCompleted,
  setSecondStepCompleted,
  setThirdStepCompleted,
  aadhaarErr,
}) => {
  const updateStep = (value) => {
    setActiveStep(parseInt(value) - 1);
  };

  const resetBtns = () => {
    setFirstStepCompleted(false);
    setSecondStepCompleted(false);
    setThirdStepCompleted(false);
  };

  return (
    <div className='signup_pages'>
      <div className='form-header '>
        <ArrowBack
          className='icon back'
          onClick={() => {
            updateStep(2);
            resetBtns();
          }}
        />
        <h2 className='form-heading'>Sign-Up</h2>
        <InfoOutlined className='icon info' />
      </div>
      <div className='second-header'>
        <h6>Additional Details</h6>
        <p>Enter some Additional Details.</p>
      </div>
      <div className='input-group'>
        <form method='POST' onSubmit={handleSubmit}>
          <div className='first-info'>
            <input
              name='aadharCardNumber'
              maxLength='12'
              minLength='12'
              value={signup?.aadharCardNumber}
              onChange={handleChange}
              className='input'
              type='text'
              placeholder='Aadhaar Card number*'
              required
            />
            {aadhaarErr && (
              <p className='text-danger'>Invalid Aadhaar Number</p>
            )}
            <input
              name='placeOfResidence'
              value={signup?.placeOfResidence}
              onChange={handleChange}
              className='input'
              type='text'
              placeholder='Place of residence*'
              required
            />
          </div>
          <button type='submit' className='continue'>
            Sign Up
          </button>
        </form>
      </div>
    </div>
  );
};

export default ThirdPage;
