import Sidebar from './Sidebar/Sidebar';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useHistory,
} from 'react-router-dom';
import Hospitalinfo from './Hospitalinfo.js';
import { Row, Col } from 'react-bootstrap';
import Navbar from './Sidebar/Navbar';
import AdditionalDetails from './AdditionalDetails';
import DoctorDetails from './DoctorDetails';
import DoctorDetailEdit from './DoctorDetails/DoctorDetailEdit.js';
import axios from 'axios';
import { useEffect, useState } from 'react';

function DynamicSideNavbar() {
  const history = useHistory();
  const id = history.location?.state?.id;
  const [hospital, setHospital] = useState({});

  useEffect(
    () =>
      id &&
      axios
        .get(`/api/hospital/details/${id}`)
        .then((res) => {
          const hospital = res.data?.hospital;
          setHospital(hospital);
        })
        .catch((err) => console.log(err)),
    []
  );

  return (
    <>
      <Router>
        <Row>
          <Col xs={2} className='mx-3'>
            <Sidebar />
          </Col>
          <Col>
            <Navbar />
            <Switch>
              <Route
                path='/dynamicNav'
                exact
                render={() => (
                  <Hospitalinfo hospital_id={id} hospital={hospital} />
                )}
              />
              <Route
                path='/hospital-info/additional-details'
                // component={AdditionalDetails}
                render={() => (
                  <AdditionalDetails hospital_id={id} hospital={hospital} />
                )}
              />
              <Route
                path='/hospital-info/doctor-details'
                component={DoctorDetails}
              />
              <Route path='/doctor-details-edit' component={DoctorDetailEdit} />
            </Switch>
          </Col>
        </Row>
      </Router>
    </>
  );
}

export default DynamicSideNavbar;
