import {
  AddAPhotoOutlined,
  AddCircleOutline,
  ArrowBack,
  InfoOutlined,
} from '@material-ui/icons';
import React from 'react';

const SecondPage = ({
  signup,
  handleChange,
  setStatus,
  handleFileChange,
  logoPreview,
  setActiveStep,
  setFirstStepCompleted,
  setSecondStepCompleted,
  setThirdStepCompleted,
}) => {
  const handleContinue = (e) => {
    e.preventDefault();
    setActiveStep(2);
    setSecondStepCompleted(true);
  };

  const updateStep = (value) => {
    setActiveStep(parseInt(value) - 1);
  };
  const resetBtns = () => {
    setFirstStepCompleted(false);
    setSecondStepCompleted(false);
    setThirdStepCompleted(false);
  };

  return (
    <div className='second-page-section signup_pages'>
      <form onSubmit={handleContinue}>
        <div className='form-header'>
          <ArrowBack
            className='icon back'
            onClick={() => {
              updateStep(1);
              resetBtns();
            }}
          />
          <h2 className='form-heading'>Sign-Up</h2>
          <InfoOutlined className='icon info' />
        </div>
        <div className='second-header'>
          <h6>Personal Details</h6>
          <p>Enter your Personal Details</p>
        </div>
        <div className='second-page-fields'>
          <div className='input-group '>
            <div className='input-second-page'>
              <input
                name='firstName'
                minLength='2'
                maxLength='30'
                value={signup?.firstName}
                onChange={handleChange}
                className='input'
                type='text'
                placeholder='Firstname*'
                required
              />
              <input
                name='middleName'
                minLength='2'
                maxLength='30'
                value={signup?.middleName}
                onChange={handleChange}
                className='input'
                type='text'
                placeholder='Middle name'
              />
              <input
                name='lastName'
                minLength='2'
                maxLength='30'
                value={signup?.lastName}
                onChange={handleChange}
                className='input'
                type='text'
                placeholder='Lastname*'
                required
              />
            </div>
            <div className='input-second-page-2'>
              <input
                name='age'
                minLength={2}
                maxLength={2}
                min={1}
                value={signup?.age}
                onChange={handleChange}
                className='input age'
                type='number'
                placeholder='Age*'
                required
              />
              <select
                name='gender'
                onChange={handleChange}
                className='input gender'
                required
              >
                <option value='male' selected={signup?.gender === 'male'}>
                  Male
                </option>
                <option value='female' selected={signup?.gender === 'female'}>
                  Female
                </option>
                <option value='others' selected={signup?.gender === 'others'}>
                  Others
                </option>
              </select>
            </div>
          </div>
          <div className='input-group-2'>
            <div className='profile-pic'>
              {logoPreview ? (
                <img src={logoPreview} className='profile-add' />
              ) : (
                <AddAPhotoOutlined className='profile-add' />
              )}
            </div>
            <div className='add-profile-pic'>
              <input
                onChange={handleFileChange}
                name='profilePic'
                className='input'
                type='file'
                accept='image/*'
              />
            </div>
          </div>
        </div>

        {/* <div className="input-group second-page-preferences">
        
      </div> */}
        <button type='submit' className='continue'>
          Continue
        </button>
      </form>
    </div>
  );
};

export default SecondPage;
