import React from "react";
import "../styles/emergencyappointment.css";

function EmergencyApppointment() {
  return (
    <>
      <section
        className="emergency-outer-box pos-rel"
        style={{ display: "none" }}
      >
        <div class="container emergency-container p-0">
          <div className="service-box">
            <div className="left">
              <div className="body">
                <h1>
                  Help us redefine <br /> healthcare, with RAH.
                </h1>

                <div className="form-line">
                  <div className="element">
                    <img src={require("../img/Name.png").default} alt="" />
                    <form class="" action="#">
                      <label for="input">Your name</label>
                      <input type="text" placeholder="Enter Your Name" />
                    </form>
                  </div>
                  <div className="element">
                    <img
                      src={require("../img/mail-download.png").default}
                      alt=""
                    />
                    <form class="" action="#">
                      <label for="input">Your Email ID</label>
                      <input type="text" placeholder="Enter Email Address" />
                    </form>
                  </div>
                </div>

                <div className="form-line">
                  <div className="element">
                    <img src={require("../img/Vector.png").default} alt="" />
                    <form className="" action="#">
                      <label htmlFor="input">Select your services</label>

                      <select
                        class="form-select"
                        aria-label="Default select example"
                      >
                        <option selected>Choose services</option>
                        <option value="1">Hospital</option>
                        <option value="2">Emergency</option>
                      </select>
                    </form>
                  </div>
                  <div className="element">
                    <img src={require("../img/phone.png").default} alt="" />
                    <form class="" action="#">
                      <label for="input">Your phone No</label>
                      <input type="text" placeholder="Enter Your Phone" />
                    </form>
                  </div>
                </div>

                <div className="row pl-20 pt-30">
                  <button className="appoint-button-2">BECOME A RAHI</button>
                </div>
              </div>
            </div>
            <div className="right">
              <img
                className="img-fluid"
                src={require("../img/appointmentbanner.png").default}
                alt=""
              />
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default EmergencyApppointment;
