import { useState, useEffect } from 'react';
import { ToggleButton } from '../Dashboard/SuperAdminToggleButton';
import React from 'react';
import { Modal, Button } from 'react-bootstrap';
import axios from 'axios';

function WishlistToggleButton({ item, key }) {
  const [toggle, settoggle] = useState(false);
  const [show, setShow] = useState(false);
  const [status, setStatus] = useState(false);
  const [items, setItems] = useState([]);
  const [filteredItems, setFilteredItems] = useState([]);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const handleDelete = (id) => {
    axios
      .delete(`/api/hospital/delete/${id}`)
      .then((res) => {
        if (res.data?.status) {
          setStatus(true);
          setShow(false);
        }
      })
      .catch((err) => console.log(err.response));
  };

  useEffect(() => {
    axios
      .get('/api/hospital/all')
      .then(function (response) {
        setItems(response.data.hospital);
        setFilteredItems(response.data.hospital);
      })
      .catch(function (error) {
        console.log(error);
      });
    return;
  }, [status]);

  console.log(items);
  return (
    <>
      <tr key={item?._id}>
        <td>{item?.hospital_name}</td>
        <td>{item?.hospital_type}</td>
        <td>{item?.contact_number}</td>
        <td>{item?.createdAt}</td>
        <td>
          <ToggleButton
            selected={toggle}
            toggleSelected={() => {
              settoggle(!toggle);
            }}
          />
        </td>
        <td>
          <button
            type='button'
            className='btn'
            // onClick={() => deleteContact(item?._id)}
            onClick={handleShow}
          >
            <i className='fas fa-trash-alt'></i>
            <br></br>
          </button>
          {/* Model */}
          <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
              <Modal.Title>Are you Sure to Delete?</Modal.Title>
            </Modal.Header>
            <Modal.Footer>
              <Button
                variant='secondary'
                onClick={() => handleDelete(item?._id)}
              >
                Yes
              </Button>
              <Button variant='secondary' onClick={handleClose}>
                No
              </Button>
            </Modal.Footer>
          </Modal>
        </td>
      </tr>
    </>
  );
}

export default WishlistToggleButton;
