import { useContext, useState, useEffect } from 'react';
import './AmbulanceSearchbar.css';
import SearchIcon from '@material-ui/icons/Search';
import { LocationContext } from '../App';
import { AmbulanceContext } from '../Ambulance/Components/mainambulance';

export default function AmbulanceSearchbar() {
  const { address } = useContext(LocationContext);
  const { origAmbulances, setAmbulances } = useContext(AmbulanceContext);
  const [searchField, setSearchField] = useState({
    location: '',
    destination: '',
  });

  const handleChange = (e) =>
    setSearchField({ ...searchField, [e.target.name]: e.target.value });

  const handleSearch = (e) => {
    e.preventDefault();
    const filteredAmbulances = origAmbulances.filter((item) =>
      item?.address.toLowerCase().includes(searchField?.location.toLowerCase())
    );
    filteredAmbulances.sort((a, b) => a?.distance - b?.distance);
    setAmbulances(filteredAmbulances);
  };

  useEffect(
    () => setSearchField({ ...searchField, location: address }),
    [address]
  );

  return (
    <div className='AmbulanceSearchbarComponent'>
      <div className='Ambulance-Bottom'>
        <form onSubmit={handleSearch}>
          <div className='Searchbar-Body'>
            <div className='Searchbar-Items'>
              <div className='LeftItem'>
                <h3>Your Location</h3>
                <input
                  type='text'
                  name='location'
                  value={searchField?.location}
                  onChange={handleChange}
                  className='border border-0'
                  placeholder='Search your area'
                />
              </div>
              <div className='RightItem'>
                <i class='fas fa-map-marker-alt'></i>
              </div>
            </div>
            {/* <div className='Searchbar-Items'>
            {' '}
            <div className='LeftItem'>
              <h3>Destination</h3>
              <input
                type='text'
                name='destination'
                value={searchField?.destination}
                onChange={handleChange}
                className='border border-0'
                placeholder='Search destination'
              />
            </div>
            <div className='RightItem'>
              <i class='fas fa-map-marker-alt'></i>
            </div>
          </div> */}
            {/* <div className='Searchbar-Items'>
            {' '}
            <div className='LeftItem'>
              <h3>Ambulance Type</h3>
              <input
                type='text'
                name='location'
                className='border border-0'
                placeholder='Select type'
              />
            </div>
            <div className='RightItem'>
              <i class='fas fa-map-marker-alt'></i>
            </div>
          </div> */}
            <div className='Searchbar-Button-Main'>
              <button type='submit' className='Searchbar-Button'>
                <h4>Search</h4>
                <SearchIcon className='search-icon' />
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}
