import React, { useState, useLayoutEffect, useContext } from 'react';
import '../styles/mapbox.css';
import { LocationContext } from '../../App';
import GoogleMapReact from 'google-map-react';
// import CallIcon from '@mui/icons-material/Call';
import { Marker, Popup } from '../../Search/components/Marker';
import DirectionsOutlinedIcon from '@mui/icons-material/DirectionsOutlined';
import { GOOGLE_MAP_API_KEY } from '../../env';

function Mapbox({ hospitals }) {
  const [selectedHospital, setSelectedHospital] = useState(null);
  const { isCoords, location } = useContext(LocationContext);
  const [selectedLoc, setSelectedLoc] = useState(null);

  const [defaultProps, setDefaultProps] = useState({
    center: {
      lat: location?.[0],
      lng: location?.[1],
    },
    zoom: 11,
  });

  const handleGetHospital = (id) => {
    const getSingleHospital =
      selectedHospital?._id === id ? null : hospitals.find((i) => i._id === id);
    setSelectedHospital(getSingleHospital);
  };

  const showPopupBox = (name, distance, location) => {
    !selectedHospital &&
      setSelectedLoc({
        hospital_name: name,
        distance,
        location,
        setSelectedLoc,
      });
    selectedHospital?.hospital_name == name &&
      setSelectedLoc({
        hospital_name: name,
        distance,
        location,
        setSelectedLoc,
      });
  };

  const closePopupBox = () => setSelectedLoc(null);

  const showInMapClicked = (address) => {
    isCoords &&
      window.open(
        `https://www.google.com/maps/dir/?api=1&destination=${address}`
      );
  };

  useLayoutEffect(() => {
    setDefaultProps({
      ...defaultProps,
      center: {
        lat: location?.[0],
        lng: location?.[1],
      },
    });
  }, [location]);

  return (
    <>
      <div className='map-box-main'>
        <div className='map-box-child'>
          <div className='map-box-left'>
            <GoogleMapReact
              bootstrapURLKeys={{
                key: GOOGLE_MAP_API_KEY,
              }}
              defaultCenter={defaultProps.center}
              defaultZoom={defaultProps.zoom}
            >
              {hospitals &&
                !selectedHospital &&
                Array.isArray(hospitals) &&
                !!hospitals.length &&
                hospitals.slice(0, 15).map((hospital, indx) => {
                  return (
                    !!hospital?.location?.coordinates && (
                      <Marker
                        key={indx}
                        lat={hospital?.location?.coordinates?.[1]}
                        lng={hospital?.location?.coordinates?.[0]}
                        setSelectedLoc={setSelectedLoc}
                        hospital={hospital}
                      />
                    )
                  );
                })}
              {selectedHospital && selectedHospital?.location?.coordinates && (
                <Marker
                  lat={selectedHospital?.location?.coordinates?.[1]}
                  lng={selectedHospital?.location?.coordinates?.[0]}
                  setSelectedLoc={setSelectedLoc}
                  hospital={selectedHospital}
                />
              )}
              {selectedLoc && (
                <Popup
                  lat={selectedLoc?.location?.coordinates?.[1]}
                  lng={selectedLoc?.location?.coordinates?.[0]}
                  name={selectedLoc?.hospital_name}
                  isCoords={isCoords}
                  distance={selectedLoc?.distance}
                  setSelectedLoc={setSelectedLoc}
                />
              )}
            </GoogleMapReact>
          </div>
          <div className='map-box-right'>
            <h2>Services near you</h2>
            <div className='body'>
              {hospitals && !!hospitals.length && (
                <div
                  onClick={() => handleGetHospital(hospitals?.[0]?._id)}
                  className='mapbox-item mapbox-item-main'
                  onMouseEnter={() =>
                    showPopupBox(
                      hospitals[0]?.hospital_name,
                      hospitals[0]?.distance,
                      hospitals[0]?.location
                    )
                  }
                  onMouseLeave={closePopupBox}
                >
                  <div className='lefter'>
                    <h3>{hospitals?.[0]?.hospital_name}</h3>
                    {isCoords && !!hospitals?.[0]?.distance && (
                      <h4>{hospitals?.[0]?.distance} kms away</h4>
                    )}
                  </div>

                  <div className='righter'>
                    <div
                      className='righter-img'
                      onClick={() => showInMapClicked(hospitals?.[0]?.address)}
                    >
                      <DirectionsOutlinedIcon />
                    </div>
                    <div className='righter-img-2'>
                      <img
                        src={require('../img/PhoneCall.png').default}
                        alt=''
                      />

                      {hospitals?.[0]?.contact_number && (
                        <div className='pop_over top_pop'>
                          <a
                            className='text active'
                            href={`tel:${hospitals?.[0]?.contact_number}`}
                          >
                            <i class='fas fa-phone'></i> &nbsp;
                            {hospitals?.[0]?.contact_number}
                          </a>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              )}

              {hospitals &&
                !!hospitals.length &&
                hospitals.slice(1, 6).map((hospital) => (
                  <div
                    onClick={() => handleGetHospital(hospital?._id)}
                    className='mapbox-item mapbox-item-main'
                    key={hospital?._id}
                    onMouseEnter={() =>
                      showPopupBox(
                        hospital?.hospital_name,
                        hospital?.distance,
                        hospital?.location
                      )
                    }
                    onMouseLeave={closePopupBox}
                  >
                    <div className='lefter'>
                      <h3>{hospital?.hospital_name}</h3>
                      {isCoords && !!hospital.distance && (
                        <h4>{hospital?.distance} kms away</h4>
                      )}
                    </div>

                    <div className='righter'>
                      <div
                        className='righter-img'
                        onClick={() => showInMapClicked(hospital?.address)}
                      >
                        <DirectionsOutlinedIcon style={{}} />
                      </div>

                      <div className='righter-img-2'>
                        <img
                          src={require('../img/PhoneCall.png').default}
                          alt=''
                        />
                        {hospital?.contact_number && (
                          <div className='pop_over top_pop'>
                            <a
                              className='text active'
                              href={`tel:${hospital?.contact_number}`}
                            >
                              <i class='fas fa-phone'></i> &nbsp;
                              {hospital?.contact_number}
                            </a>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                ))}

              {/* <div className='mapbox-item'>
                <div className='lefter'>
                  <h3>Sunshine Hospital</h3>
                  <h4>3 kms away</h4>
                </div>
                <div className='righter'>
                  <div className='righter-img'>
                    <img src={require('../img/mail.png').default} alt='' />
                  </div>
                  <div className='righter-img-2'>
                    <img
                      style={{ height: '24px', width: '24px' }}
                      src={require('../img/phone.png').default}
                      alt=''
                    />
                  </div>
                </div>
              </div>
              <div className='mapbox-item'>
                <div className='lefter'>
                  <h3>Sunshine Hospital</h3>
                  <h4>3 kms away</h4>
                </div>
                <div className='righter'>
                  <div className='righter-img'>
                    <img src={require('../img/mail.png').default} alt='' />
                  </div>
                  <div className='righter-img-2'>
                    <img
                      style={{ height: '24px', width: '24px' }}
                      src={require('../img/phone.png').default}
                      alt=''
                    />
                  </div>
                </div>
              </div>
              <div className='mapbox-item'>
                <div className='lefter'>
                  <h3>Sunshine Hospital</h3>
                  <h4>3 kms away</h4>
                </div>
                <div className='righter'>
                  <div className='righter-img'>
                    <img src={require('../img/mail.png').default} alt='' />
                  </div>
                  <div className='righter-img-2'>
                    <img
                      style={{ height: '24px', width: '24px' }}
                      src={require('../img/phone.png').default}
                      alt=''
                    />
                  </div>
                </div>
              </div> */}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
export default Mapbox;
