import { useEffect, useState } from "react";
import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";
import paginationFactory from "react-bootstrap-table2-paginator";
import BootstrapTable from "react-bootstrap-table-next";
import DirectionsOutlinedIcon from "@mui/icons-material/DirectionsOutlined";

export default function AmbulanceTable({ ambulances, isCoords }) {
  const [data, setData] = useState([]);

  const showInMapClicked = (address) => {
    isCoords &&
      window.open(
        `https://www.google.com/maps/dir/?api=1&destination=${address}`
      );
  };

  useEffect(() => setData(ambulances), [ambulances]);

  function priceFormatter(cell, row) {
    return (
      <div className="column-img">
        <div className="righter-img-mail">
          <div
            style={{ cursor: "pointer" }}
            onClick={() => showInMapClicked(`${row?.address} ${row?.pincode}`)}
          >
            <DirectionsOutlinedIcon />
          </div>
          {/* <img
            src={require('../../emergencyPage/img/mail.png').default}
            alt=''
          /> */}
        </div>
        <div className="righter-img-phone">
          <a href={`tel:${row?.contact_number}`}>
            <img
              src={require("../../emergencyPage/img/PhoneCall.png").default}
              alt=""
            />
          </a>
        </div>
      </div>
    );
  }
  const columns = [
    {
      dataField: "org_type",
      text: "Type",
      headerStyle: {
        font: "Inter",
        fontStyle: "semi-bold",
        fontWeight: " 600",
        fontSize: "14px",
        color: "#4C4C4C",
        backgroundColor: "#F9FAFB",
      },
    },
    {
      dataField: "org_name",
      text: "Ambulance Name",
      headerStyle: {
        font: "Inter",
        fontStyle: "semi-bold",
        fontWeight: " 600",
        fontSize: "14px",
        color: "#4C4C4C",
        backgroundColor: "#F9FAFB",
      },
    },
    // {
    //   dataField: "price",
    //   text: "price ",
    //   headerStyle: {
    //     font: "Inter",
    //     fontStyle: "semi-bold",
    //     fontWeight: " 600",
    //     fontSize: "14px",
    //     color: "#4C4C4C",
    //     backgroundColor: "#F9FAFB",
    //   },
    // },
    {
      dataField: "ambulance_facility",
      text: "Facility",
      headerStyle: {
        font: "Inter",
        fontStyle: "semi-bold",
        fontWeight: " 600",
        fontSize: "14px",
        color: "#4C4C4C",
        backgroundColor: "#F9FAFB",
      },
    },
    {
      dataField: "distance",
      text: "Distance radius",
      headerStyle: {
        font: "Inter",
        fontStyle: "semi-bold",
        fontWeight: " 600",
        fontSize: "14px",
        color: "#4C4C4C",
        backgroundColor: "#F9FAFB",
      },
    },
    {
      dataField: "contact_number",
      text: "Ambulance Number",
      headerStyle: {
        font: "Inter",
        fontStyle: "semi-bold",
        fontWeight: " 600",
        fontSize: "14px",
        color: "#4C4C4C",
        backgroundColor: "#F9FAFB",
      },
    },
    {
      dataField: "Icons",
      text: " ",
      formatter: priceFormatter,
    },
  ];
  const options = {
    paginationSize: 4,
    pageStartIndex: 1,
    // alwaysShowAllBtns: true, // Always show next and previous button
    // withFirstAndLast: false, // Hide the going to First and Last page button
    // hideSizePerPage: true, // Hide the sizePerPage dropdown always
    // hidePageListOnlyOnePage: true, // Hide the pagination list when only one page
    firstPageText: "First",
    prePageText: "Back",
    nextPageText: "Next",
    lastPageText: "Last",
    nextPageTitle: "First page",
    prePageTitle: "Pre page",
    firstPageTitle: "Next page",
    lastPageTitle: "Last page",

    disablePageTitle: true,
    sizePerPageList: [5],
  };
  return (
    <>
      <div className="boot-table">
        <BootstrapTable
          keyField="_id"
          columns={columns}
          data={data}
          bordered={false}
          pagination={paginationFactory(options)}
        />
      </div>
    </>
  );
}
