import React, { useState } from "react";
import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng,
} from "react-places-autocomplete";
import "./searchBar.css";
import SearchIcon from "@material-ui/icons/Search";

export default function SearchBar({
  dropdown,
  btnLoading,
  searchField,
  handleDropdownClick,
  handleSearchChange,
  handleSearchSubmit,
}) {
  const [address, setAddress] = useState("");
  const [initLoc, setInitLoc] = useState(true);
  // const [coordinates, setCoordinates] = useState({
  //   lat: null,
  //   lng: null,
  // });
  const handleSelect = async (value) => {
    const results = await geocodeByAddress(value);
    const latLng = await getLatLng(results[0]);
    setAddress(value);
    // setCoordinates(latLng);
  };
  return (
    <PlacesAutocomplete
      value={address}
      onChange={setAddress}
      onSelect={handleSelect}
    >
      {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
        <div className="SearchBarComponent">
          <div className="bottom">
            <div className="search-body">
              <div className="search-item">
                <div className="left">
                  <h3>Your Location</h3>

                  <div>
                    {/* <p>Latitude: {coordinates.lat}</p>
            <p>Longitude: {coordinates.lng}</p> */}

                    {initLoc ? (
                      <input
                        type="text"
                        name="location"
                        value={searchField.location}
                        onChange={(e) => {
                          handleSearchChange(e);
                          setInitLoc(false);
                        }}
                        className="border border-0"
                      />
                    ) : (
                      <input
                        type="text"
                        name="location"
                        value={searchField.location}
                        onChange={(e) => {
                          handleSearchChange(e);
                          setInitLoc(false);
                        }}
                        className="border border-0"
                        {...getInputProps({
                          placeholder: "Search your location",
                        })}
                      />
                    )}
                  </div>

                  {/* <input
                type="text"
                name="location"
                value={searchField.location}
                onChange={handleSearchChange}
                className="border border-0"
                placeholder="Search your location"
              /> */}
                  {/* <textarea>Search your area</textarea> */}
                </div>
                <div className="right">
                  <img src="./locationicon.png" />
                </div>
              </div>

              <div className="suggestion_auto">
                {/* {loading ? <div>...loading</div> : null} */}

                {suggestions.map((suggestion) => {
                  const style = {
                    backgroundColor: suggestion.active ? "#41b6e6" : "#fff",
                  };

                  return (
                    <div {...getSuggestionItemProps(suggestion, { style })}>
                      {suggestion.description}
                    </div>
                  );
                })}
              </div>

              <div className="search-item">
                <div className="left input_suggestion">
                  <h3>Search</h3>
                  <input
                    type="text"
                    name="search"
                    value={searchField.search}
                    onChange={handleSearchChange}
                    className="border border-0"
                    placeholder="Search for disease and department"
                  />
                  <div className="suggestion_dropdown">
                    {dropdown &&
                      Array.isArray(dropdown) &&
                      !!dropdown.length &&
                      dropdown
                        .slice(0, 10)
                        .map((drp, indx) => (
                          <input
                            key={indx}
                            type="text"
                            value={drp}
                            onChange={() => false}
                            onClick={handleDropdownClick}
                            className="drp_res"
                          />
                        ))}
                  </div>
                  {/* <textarea>Search anything</textarea> */}
                </div>
                <div className="search-button" onClick={handleSearchSubmit}>
                  {btnLoading ? (
                    <>
                      <h4>Searching</h4>
                      <i class="fas fa-spinner rotate_icon"></i>
                    </>
                  ) : (
                    <>
                      <h4>Search</h4>
                      <SearchIcon className="search-icon" />
                    </>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </PlacesAutocomplete>
  );
}
