import { useState, useEffect, useContext } from 'react';

import Table from './table';
import Mapbox from './map-box';
// import EmergencyHeader from './emergency-header';
import Footer from '../../landingPage/components/footer';
import EmergencyApppointment from './emergency-appointment';
import axios from 'axios';
import { LocationContext } from '../../App';
import Navbar from '../../NavigationBar/Navbar';
import EmergencyBanner from './emergencyBanner';
import { Helmet } from 'react-helmet';
import Preloader from '../../Preloader';
import FooterCredit from '../../Footer_Credit/FooterCredit';

function MainEmergency() {
  const { isCoords, location } = useContext(LocationContext);

  const [hospitals, setHospitals] = useState([]);
  const [hospitalsTable, setHospitalsTable] = useState([]);
  const [preloading, setPreloading] = useState(true);

  useEffect(() => {
    axios
      .get('/api/hospital/all?page=1', {
        headers: {
          location: JSON.stringify(location),
        },
      })
      .then((res) => {
        if (res.data?.hospital) {
          setPreloading(false);
          let hospitals = res.data?.hospital;
          hospitals = hospitals.sort((a, b) => a?.distance - b?.distance);
          setHospitals(hospitals);
          setHospitalsTable(hospitals);
        }
      })
      .catch((err) => console.log(err.response));
  }, [location]);

  return (
    <div className='emergency-page'>
      <Helmet>
        <title>Emergency Page</title>
      </Helmet>
      {preloading ? (
        <Preloader />
      ) : (
        <>
          <Navbar />
          <EmergencyBanner setHospitals={setHospitals} />
          <Mapbox hospitals={hospitals} />
          {/* <EmergencyHeader /> */}
          <br />
          <br />
          <br />
          <Table isCoords={isCoords} hospitals={hospitalsTable} />
          <EmergencyApppointment />
          <br />
          <br />
          <br />
          <Footer />
          <FooterCredit />
        </>
      )}
    </div>
  );
}
export default MainEmergency;
