import React from "react";
import "./searchBar.css";
import SearchIcon from "@material-ui/icons/Search";

export default function EmergencySearchBar({
  dropdown,
  btnLoading,
  searchField,
  handleDropdownClick,
  handleSearchChange,
  handleSearchSubmit,
}) {
  return (
    <div className="SearchBarComponent">
      <div className="bottom">
        <div className="search-body">
          <div className="search-item">
            <div className="left">
              <h3>Your Location</h3>
              <input
                type="text"
                name="location"
                value={searchField.location}
                onChange={handleSearchChange}
                className="border border-0"
                placeholder="Search your area"
              />
              {/* <textarea>Search your area</textarea> */}
            </div>
            <div className="right">
              <img src="./locationicon.png" />
            </div>
          </div>

          <div className="search-item">
            <div className="left input_suggestion">
              <h3>Problem Organ</h3>
              <input
                type="text"
                name="search"
                value={searchField.search}
                onChange={handleSearchChange}
                className="border border-0"
                placeholder="For specific organ"
              />
              <div className="suggestion_dropdown">
                {dropdown &&
                  Array.isArray(dropdown) &&
                  !!dropdown.length &&
                  dropdown
                    .slice(0, 4)
                    .map((drp, indx) => (
                      <input
                        key={indx}
                        type="text"
                        value={drp}
                        onChange={() => false}
                        onClick={handleDropdownClick}
                        className="drp_res"
                      />
                    ))}
              </div>
              {/* <textarea>Search anything</textarea> */}
            </div>
            <div className="search-button" onClick={handleSearchSubmit}>
              {btnLoading ? (
                <>
                  <h4>Searching</h4>
                  <i class="fas fa-spinner rotate_icon"></i>
                </>
              ) : (
                <>
                  <h4>Search</h4>
                  <SearchIcon className="search-icon" />
                </>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
