import CreateHospitalForm from './CreateHospitalForm';
import './createhospital.scss';
import { Helmet } from 'react-helmet';
import MainNavbar from '../NavigationBar/Navbar';
import Footer from '../landingPage/components/footer';

const CreateHospital = ({ user, hospital_id }) => {
  console.log('form ', hospital_id);
  return (
    <>
      <Helmet>
        <title>Create A New Hospital</title>
      </Helmet>
      <MainNavbar />
      <div className='container container_sec'>
        <CreateHospitalForm
          hospital_id={hospital_id ? hospital_id : user?.hospital_id}
        />
      </div>
      <Footer />
    </>
  );
};

export default CreateHospital;
