import React, { useState } from 'react';
import '../styles/Departments.css';
// mui icons
import MedicationIcon from '@mui/icons-material/Medication';

export default function Departments({ department }) {
  const [items, setItems] = useState({
    more: false,
    start: 0,
    end: 10,
  });

  const handleSeeMore = () =>
    setItems({
      ...items,
      more: !items.more,
      end: items.more ? 10 : department.length,
    });

  return (
    <div className='hos-detail-dep'>
      <div className='departments'>
        <div className='d-flex justify-content-between'>
          <div className='header6'>Departments</div>
          <p onClick={handleSeeMore} style={{ cursor: 'pointer' }}>
            <strong> See {items.more ? 'less' : 'more'}</strong>
          </p>
        </div>
        <div className='cards6'>
          {department &&
            department.slice(items.start, items.end).map(
              (dept, indx) =>
                dept && (
                  <div className='card6' key={indx}>
                    <div className='icon6'>
                      <MedicationIcon className='iconMain6' />
                    </div>
                    <div className='title6 text-capitalize'>{dept}</div>
                  </div>
                )
            )}
        </div>
      </div>
    </div>
  );
}
