import React, { Component } from 'react';
import Slider from 'react-slick';
import './MultiItem.css';

import LeftArrow from '../rah-assets/arrow-circle-right.png';
import RightArrow from '../rah-assets/arrow-circle-right (1).png';
const SlickArrowLeft = ({ currentSlide, slideCount, ...props }) => (
  <img src={LeftArrow} alt='prevArrow' {...props} style={{ top: '117%' }} />
);

const SlickArrowRight = ({ currentSlide, slideCount, ...props }) => (
  <img src={RightArrow} alt='nextArrow' {...props} style={{ top: '117%' }} />
);

export default function SwipeToSlide({ images }) {
  const settings = {
    dots: true,
    className: 'center',
    infinite: true,

    slidesToShow: 1,
    slidesToScroll: 1,
    initialSlide: 0,
    nextArrow: <SlickArrowLeft />,
    prevArrow: <SlickArrowRight />,
    dotsClass: 'slick-dots custom-indicator',
    customPaging: function (i) {
      return (
        <img
          src={images[i]}
          alt='hi'
          style={{
            width: '110px',
            height: '130px',
            objectFit: 'cover',
          }}
        />
      );
    },
    responsive: [
      {
        breakpoint: 524,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
        },
      },
    ],
  };
  return (
    <Slider {...settings}>
      {images &&
        images.map((img, indx) => (
          <div key={indx}>
            <img style={{ height: '350px', width: '99%' }} src={img} alt='' />
          </div>
        ))}
    </Slider>
  );
}
