import React from "react";
import { Table } from "react-bootstrap";
import axios from "axios";
import { useState, useEffect } from "react";
import { Button, InputGroup, FormControl, Modal } from "react-bootstrap";
import "../App.css";
import { Link, useHistory } from "react-router-dom";

// import Pagination from 'react-js-pagination';
// import "bootstrap/less/bootstrap.less";

function Main() {
  const history = useHistory();
  const [tableMenu, setTableMenu] = useState([
    "hospital_name",
    "hospital_type",
    // 'about',
    "total_seats",
    "total_icus",
    // 'logo',
    // 'images',
    "contact_number",
    "hospital_email",
    "address",
    "department",
    "logo",
    "images",
    // "facilities",
    // 'rating',
    // 'createdAt',
  ]);
  const StyleImg = {
    height: "70px",
    width: "70px",
    objectFit: "cover",
    margin: "4px",
  };
  const [tableLogo, setTableLogo] = useState(["logo"]);
  const [items, setItems] = useState([]);
  const [filteredItems, setFilteredItems] = useState([]);
  // const [page, setPage] = useState(15);
  const [show, setShow] = useState(false);
  // input filed for filter hospitals
  const [search, setSearch] = useState("");
  const [status, setStatus] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  // handle search input filter
  const handleSearchFilter = (e) => {
    setSearch(e.target.value);
    if (!!e.target.value) {
      const filteredItems =
        !!items.length &&
        items.filter((item) =>
          item?.hospital_name.toLowerCase().includes(search)
        );
      // console.log('filtered ', filteredItems);
      setFilteredItems(filteredItems);
    } else {
      setFilteredItems(items);
    }
  };

  // Pagination
  // const handlePageChange = (pageNumber) => {
  //   console.log(`active page is ${pageNumber}`);
  //   setPage(pageNumber);
  // };

  // const removeTodo = (index) => {
  //   const newTodos = [...items];
  //   newTodos.splice(index, 1);
  //   setItems(newTodos);
  // };
  const handleDelete = (id) => {
    axios
      .delete(`/api/hospital/delete/${id}`)
      .then((res) => {
        if (res.data?.status) {
          setStatus(true);
          setShow(false);
        }
      })
      .catch((err) => console.log(err.response));
  };

  useEffect(() => {
    axios
      .get("/api/hospital/all")
      .then(function (response) {
        // handle success
        console.log(response.data.hospital);
        setItems(response.data.hospital);
        setFilteredItems(response.data.hospital);
      })
      .catch(function (error) {
        // handle error
        console.log(error.response);
      });
    return;
  }, [status]);

  return (
    <>
      <div className="main_box p-2 m-2 main_box_border">
        <p>
          Hospitals <span> {items.length} hospitals</span>
        </p>
        <div className="main_button">
          <div className="main_button_start">
            <Button
              variant="outline-secondary"
              style={{
                borderRight: "none",
                borderTopRightRadius: "0px",
                borderBottomRightRadius: "0px",
              }}
            >
              View all
            </Button>
            <Button
              variant="outline-secondary"
              style={{ borderRadius: "0px", borderRight: "none" }}
            >
              Active
            </Button>
            <Button
              variant="outline-secondary"
              style={{
                borderBottomLeftRadius: "0px",
                borderTopLeftRadius: "0px",
              }}
            >
              Archived
            </Button>
          </div>
          <div className="main_button_end">
            <InputGroup className="mb-3 main_button_search">
              <FormControl
                value={search}
                onChange={handleSearchFilter}
                aria-label="Search"
                placeholder="Search"
                style={{ width: "100%" }}
              />
            </InputGroup>

            <Button
              variant="success"
              className="ml-2 main_button_Archive"
              style={{ height: "32px" }}
            >
              <i className="fas fa-archive" style={{ marginRight: "10px" }}></i>
              Archive
            </Button>
          </div>
        </div>
        <Table style={{ width: "20vh" }}>
          <thead>
            <tr>
              <th>Action</th>
              <th></th>
              <th>Status</th>
              {tableMenu.map((menu) => (
                <th key={menu} className="text-capitalize">
                  {menu.split("_").join(" ")}
                </th>
              ))}

              {/* <th>Facilities</th> */}
            </tr>
          </thead>

          <tbody>
            {!!filteredItems.length &&
              filteredItems.map((item) => (
                <tr key={item?._id}>
                  {" "}
                  <td>
                    <button
                      type="button"
                      className="btn"
                      // onClick={() => deleteContact(item?._id)}
                      onClick={handleShow}
                    >
                      <i class="fas fa-trash-alt"></i>
                    </button>
                    {/* Model */}
                    <Modal show={show} onHide={handleClose}>
                      <Modal.Header closeButton>
                        <Modal.Title>Are you Sure to Delete ?</Modal.Title>
                      </Modal.Header>
                      <Modal.Footer>
                        <Button
                          variant="secondary"
                          onClick={() => handleDelete(item?._id)}
                        >
                          Yes
                        </Button>
                        <Button variant="secondary" onClick={handleClose}>
                          No
                        </Button>
                      </Modal.Footer>
                    </Modal>
                  </td>{" "}
                  <td>
                    {/* <Link
                      to={`/admin/update/${item?._id}`} */}
                    {/* // to={`/dynamicNav/${item?._id}`} */}
                    {/* >
                      <i className="fas fa-pencil-alt px-2"></i>
                    </Link> */}
                    <div
                      style={{ cursor: "pointer" }}
                      onClick={() =>
                        history.push(`/admin/update/${item?._id}`, {
                          id: item?._id,
                        })
                      }
                    >
                      <i className="fas fa-pencil-alt px-2"></i>
                    </div>
                  </td>
                  <td>Active</td>
                  {!!tableMenu &&
                    tableMenu.map((menu) => (
                      <td key={menu}>
                        {/* <p style={{ maxHeight: '100px', overflow: 'hidden' }}> */}
                        {menu === "department" &&
                          item[menu].map((i) => i + ", ")}
                        {menu === "logo" && (
                          <img style={StyleImg} src={item[menu]} alt="logo" />
                        )}
                        <div className="d-flex ">
                          {menu === "images" &&
                            item[menu].map((i) => (
                              <img
                                key={i}
                                style={StyleImg}
                                src={i}
                                alt="hospital_image"
                              />
                            ))}
                        </div>
                        {!["department", "images", "logo"].includes(menu) &&
                          item[menu]}
                        {/* </p> */}
                      </td>
                    ))}
                  {/* <td>{item?.facilities}</td> */}
                  {/* <td>{item?.hospital_type}</td>
                  <td>{item?.contact_number}</td>
                  <td>{item?.createdAt}</td> */}
                  {/* <td><img src={} alt="" /></td> */}
                  {tableLogo.map((menuLogo) => {
                    <td key={menuLogo}>
                      <img src={item[menuLogo]} alt="logo" />
                    </td>;
                  })}
                </tr>
              ))}
          </tbody>
        </Table>
      </div>
    </>
  );
}

export default Main;
