import React, { useEffect } from "react";
import "./AdditionalDetails.css";
import { Table, Container, Row, Col, Button } from "react-bootstrap";
import { useState } from "react";
import Dropdown from "../images/Vector.png";
import FeatureDetails from "./FeatureDetailsTable";
import ValueServices from "./ValueServicesTable";
import { Link, useHistory } from "react-router-dom";
import axios from "axios";

export default function AdditionalDetails({ hospital_id, hospital }) {
  const history = useHistory();
  const [items, setItems] = useState([]);
  const [state, setState] = useState({
    heading: "",
    icon: "",
    content: "",
  });
  const [vasitems, setVasItems] = useState([]);
  const [vasstate, setVasState] = useState({
    vname: "",
    icon: "",
    vdescription: "",
  });
  const handleChange = (e) => {
    setState({
      ...state,
      [e.target.name]: [e.target.value],
    });
  };
  const handleVasChange = (e) => {
    setVasState({
      ...vasstate,
      [e.target.name]: [e.target.value],
    });
  };
  const handleEarse = () => {
    setState({
      heading: "",
      content: "",
      icon: "",
    });
  };
  const handleFormSubmit = (e) => {
    e.preventDefault();

    state.heading && state.content && setItems([...items, state]);
    setState({
      heading: "",
      content: "",
      icon: "",
    });
    console.log(items);
  };
  const handleVasFormSubmit = (e) => {
    e.preventDefault();

    setVasItems([...vasitems, vasstate]);
    setVasState({
      vname: " ",
      vdescription: " ",
      icon: "",
    });
  };
  const [isHover, setIsHover] = useState(false);
  const [isHover2, setIsHover2] = useState(false);
  const [curFacility, setCurFacility] = useState({
    icon: "",
  });
  const [curFacility2, setCurFacility2] = useState({
    icon: "",
  });
  const facility_icons = [
    "alarm-warning-line.png",
    "check-double-line.png",
    "search-eye-line.png",
    "git-repository-line.png",
    "history-line.png",
    "health-book-line.png",
    "hand-sanitizer-line.png",
    "Vector.png",
    "lungs-line.png",
    "scissors-line.png",
    "ruler-line.png",
    "sip-line.png",
    "truck-line.png",
    "road-map-line.png",
    "shield-line.png",
    "rest-time-line.png",
    "macbook-line.png",
    "medicine-bottle-line.png",
    "contrast-drop-2-line.png",
    "ink-bottle-line.png",
    "edit-2-line.png",
    "drop-line.png",
    "eye-line.png",
    "thumb-up-line.png",
    "heart-pulse-line.png",
    "mac-line.png",
    "mental-health-line.png",
    "store-3-line.png",
    "store-2-line.png",
    "Vector1.png",
    "Vector2.png",
    "mail-unread-line.png",
    "psychotherapy-line.png",
    "capsule-line.png",
    "Vector3.png",
    "microscope-line.png",
    "home-2-line.png",
    "flag-2-line.png",
    "Vector4.png",
    "arrow-left-right-line.png",
    "filter-line.png",
    "heart-add-line.png",
    "git-repository-private-line.png",
    "shut-down-line.png",
    "nurse-line.png",
    "Vector5.png",
    "briefcase-4-line.png",
    "inbox-unarchive-line.png",
    "message-2-line.png",
    "pulse-line.png",
    "task-line.png",
    "sticky-note-line.png",
    "smartphone-line.png",
    "hand-heart-line.png",
    "swap-line.png",
    "secure-payment-line.png",
    "walk-line.png",
    "heart-line.png",
    "surgical-mask-line.png",
    "stethoscope-line.png",
    "server-line.png",
    "gift-2-line.png",
    "vip-line.png",
    "24-hours-line.png",
    "shopping-basket-line.png",
    "todo-line.png",
    "infrared-thermometer-line.png",
    "hearts-line.png",
    "flask-line.png",
    "zzz-line.png",
    "virus-line.png",
    "thermometer-line.png",
    "test-tube-line.png",
    "syringe-line.png",
    "dossier-line.png",
    "empathize-line.png",
    "first-aid-kit-line.png",
  ];
  const handleIconClick = (icon) => {
    setState({ ...state, icon });
    setIsHover(false);
  };
  const handleIconClick2 = (icon) => {
    setVasState({ ...vasstate, icon });
    setIsHover2(false);
  };

  const handleSaveDetails = async (e) => {
    e.preventDefault();
    try {
      const formData = new FormData();

      if (!!items.length) {
        const facilityList = JSON.stringify(items);
        formData.append("facilities", facilityList);
      }

      if (!!hospital_id) {
        console.log("update part---");
        history.push("/dynamicNav");

        const res = await axios.patch(
          `/api/hospital/update/${hospital_id}`,
          formData,
          {
            headers: { "Content-Type": "multipart/form-data" },
          }
        );
        if (res?.statusText) {
          console.log(res);
          // setStatus({ success: 'Hospital data updated' });
        } else {
          // setStatus({ error: 'Unable to insert data' });
        }
      } else {
        console.log("---create part");
        history.push("/dynamicNav");

        const res = await axios.post("/api/hospital/create", formData, {
          headers: { "Content-Type": "multipart/form-data" },
        });
        if (res?.statusText) {
          console.log(res);
          // setStatus({ success: 'Hospital data inserted' });
        } else {
          // setStatus({ error: 'Unable to insert data' });
        }
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => setItems(hospital?.facilities), [hospital]);

  return (
    <>
      <div className="AdditionalDetails-Main">
        <div className="AdditionalDetails-Body">
          <div className="Facilities-Details">
            <div className="Facility-Heading">
              <div className="texter">Facilities Details:</div>
            </div>
            <form className="Facility-form">
              <div className="Facility-form-1">
                <label className="form-label-2" for="FacilitiesName">
                  Facilities Name
                </label>
                <input
                  onChange={handleChange}
                  value={state.heading}
                  name="heading"
                  type="text"
                  className="form-control"
                  id="FacilitiesName"
                  aria-describedby="FacilitiesName"
                />
              </div>
              <div className="Facility-form-2">
                <label for="Icon" className="form-label-2">
                  Icon
                </label>
                {/* <select
                  className="form-select"
                  aria-label="Default select example"
                >
                  <option selected>Choose the suitable icon</option>
                  <option value="1">One</option>
                  <option value="2">Two</option>
                  <option value="3">Three</option>
                </select> */}
                <div className=" icon_box">
                  <Button onClick={() => setIsHover(!isHover)} variant="light">
                    {!!state?.icon ? (
                      <img
                        className="icons_show"
                        src={`${process.env.PUBLIC_URL}/images/facilities/${state?.icon}`}
                        alt=" icon1"
                      />
                    ) : (
                      "Choose the icon "
                    )}
                  </Button>
                  {isHover && (
                    <div className="icon_content">
                      <Container fluid>
                        <Row>
                          {facility_icons.map((icon, indx) => (
                            <Col
                              xs={3}
                              md={1}
                              key={indx}
                              onClick={() => handleIconClick(icon)}
                            >
                              <img
                                className="icons_show"
                                src={`${process.env.PUBLIC_URL}/images/facilities/${icon}`}
                                alt="icon1"
                              />
                            </Col>
                          ))}
                        </Row>
                      </Container>
                    </div>
                  )}
                </div>
              </div>
              <div className="Facility-form-3">
                <label for="FacilitiesDescription" className="form-label-2">
                  Facilities Description
                </label>
                <input
                  onChange={handleChange}
                  name="content"
                  value={state.content}
                  type="text"
                  className="form-control"
                  id="FacilitiesDescription"
                  aria-describedby="FacilitiesDescription"
                />
              </div>
              <div className="Facility-form-5" onClick={handleEarse}>
                <img src={require("../images/Group.png").default} />
              </div>
              <Button className="Facility-form-4" onClick={handleFormSubmit}>
                <div
                  className="texter"
                  style={{ color: "white", marginRight: "5px" }}
                >
                  Save
                </div>
                <i
                  class="fa fa-plus"
                  aria-hidden="true"
                  style={{ color: "white" }}
                ></i>
              </Button>
            </form>
            <div className="Features-Table">
              <FeatureDetails items={items} />
            </div>
          </div>
          <div className="Procedures">
            <div className="Procedures-Heading">
              <div className="texter">Procedures</div>
              <div className="Edit-Icon">
                <img src={require("../images/EditIcon.png").default} />
              </div>
            </div>
            <div className="Procedures-Heading-2">Procedure Names</div>
            <form className="Procedure-form">
              <div className="Procedures-form-1">
                <input
                  type="text"
                  className="form-control"
                  id="FacilitiesName"
                  aria-describedby="FacilitiesName"
                />
              </div>
              <div className="UpIcon">
                <img src={require("../images/up.png").default} />
              </div>
              <div className="Procedures-form-1">
                <input
                  type="text"
                  className="form-control"
                  id="FacilitiesName"
                  aria-describedby="FacilitiesName"
                />
              </div>
              <div className="UpIcon">
                <img src={require("../images/up.png").default} />
              </div>
              <div className="Procedures-form-1">
                <input
                  type="text"
                  className="form-control"
                  id="FacilitiesName"
                  aria-describedby="FacilitiesName"
                />
              </div>
              <div className="UpIcon">
                <img src={require("../images/up.png").default} />
              </div>
              <div className="Procedures-form-1">
                <input
                  type="text"
                  className="form-control"
                  id="FacilitiesName"
                  aria-describedby="FacilitiesName"
                />
              </div>
              <div className="UpIcon">
                <img src={require("../images/up.png").default} />
              </div>
              <div className="Procedures-form-1">
                <input
                  type="text"
                  className="form-control"
                  id="FacilitiesName"
                  aria-describedby="FacilitiesName"
                />
              </div>
              <div className="UpIcon">
                <img src={require("../images/up.png").default} />
              </div>
              <div className="Procedures-form-1">
                <input
                  type="text"
                  className="form-control"
                  id="FacilitiesName"
                  aria-describedby="FacilitiesName"
                />
              </div>
              <button className="Procedures-button">
                <div
                  className="texter"
                  style={{ color: "white", marginRight: "5px" }}
                >
                  More
                </div>
                <i
                  class="fa fa-plus"
                  aria-hidden="true"
                  style={{ color: "white" }}
                ></i>
              </button>
            </form>
          </div>
          <div className="Facilities-Details">
            <div className="Facility-Heading">
              <div className="texter">Value Added Services</div>
            </div>
            <form className="Facility-form">
              <div className="Facility-form-1">
                <label className="form-label-2" for="FacilitiesName">
                  VAS Name
                </label>
                <input
                  name="vname"
                  onChange={handleVasChange}
                  value={vasstate.vname}
                  type="text"
                  className="form-control"
                  id="FacilitiesName"
                  aria-describedby="FacilitiesName"
                />
              </div>
              <div className="Facility-form-2">
                <label for="Icon" className="form-label-2">
                  Icon
                </label>
                {/* <select
                  className="form-select"
                  aria-label="Default select example"
                >
                  <option selected>Choose the suitable icon</option>
                  <option value="1">One</option>
                  <option value="2">Two</option>
                  <option value="3">Three</option>
                </select> */}

                <div className=" icon_box">
                  <Button
                    onClick={() => setIsHover2(!isHover2)}
                    variant="light"
                  >
                    {!!vasstate?.icon ? (
                      <img
                        className="icons_show"
                        src={`${process.env.PUBLIC_URL}/images/facilities/${vasstate?.icon}`}
                        alt="icon1"
                      />
                    ) : (
                      "Choose the icon "
                    )}
                  </Button>
                  {isHover2 && (
                    <div className="icon_content">
                      <Container fluid>
                        <Row>
                          {facility_icons.map((icon, indx) => (
                            <Col
                              xs={3}
                              md={1}
                              key={indx}
                              onClick={() => handleIconClick2(icon)}
                            >
                              <img
                                className="icons_show"
                                src={`${process.env.PUBLIC_URL}/images/facilities/${icon}`}
                                alt="icon1"
                              />
                            </Col>
                          ))}
                        </Row>
                      </Container>
                    </div>
                  )}
                </div>
              </div>

              <div className="Facility-form-3">
                <label for="FacilitiesDescription" className="form-label-2">
                  VAS Description
                </label>
                <input
                  name="vdescription"
                  value={vasstate.vdescription}
                  onChange={handleVasChange}
                  type="text"
                  className="form-control"
                  id="FacilitiesDescription"
                  aria-describedby="FacilitiesDescription"
                />
              </div>
              <div className="Facility-form-5">
                <img src={require("../images/Group.png").default} />
              </div>
              <Button className="Facility-form-4" onClick={handleVasFormSubmit}>
                <div
                  className="texter"
                  style={{ color: "white", marginRight: "5px" }}
                >
                  Save
                </div>
                <i
                  class="fa fa-plus"
                  aria-hidden="true"
                  style={{ color: "white" }}
                ></i>
              </Button>
            </form>
            <div className="Features-Table">
              <ValueServices />
            </div>
          </div>
          <div className="Facilities-Details-2">
            <Link to={"/dynamicNav"}>
              <Button className="Back-Button">
                <i class="fas fa-angle-left" style={{ color: "black" }}></i>
              </Button>
            </Link>
            <div className="Save-Button">
              <img src={require("../images/save.png").default} />
              <div
                onClick={handleSaveDetails}
                style={{
                  color: "white",
                  marginLeft: "5px",
                }}
              >
                Save Details
              </div>
            </div>
            <Link to={"/hospital-info/doctor-details"}>
              <Button className="Back-Button">
                <i class="fas fa-angle-right" style={{ color: "black" }}></i>
              </Button>
            </Link>
          </div>
        </div>
      </div>
    </>
  );
}
