import { useState } from 'react';
import SecondPage from './SecondPage';
import FirstPage from './FirstPage';
import ThirdPage from './ThirdPage';
import './register.css';
import { Thankyou } from '../Thankyou';
import axios from 'axios';
import verifyAadhaar from './verifyAadhaar';

const Register = () => {
  let [activeStep, setActiveStep] = useState(0);
  const [firstStepCompleted, setFirstStepCompleted] = useState(false);
  const [secondStepCompleted, setSecondStepCompleted] = useState(false);
  const [thirdStepCompleted, setThirdStepCompleted] = useState(false);

  const [signup, setSignup] = useState({
    firstName: '',
    middleName: '',
    lastName: '',
    email: '',
    password: '',
    conPassword: '',
    age: '',
    gender: '',
    placeOfResidence: '',
    aadharCardNumber: '',
    isHospitalAdmin: true,
    isRahAdmin: false,
  });
  const [logoPreview, setLogoPreview] = useState();
  const [logo, setLogo] = useState();
  const [status, setStatus] = useState({
    success: '',
    error: '',
  });
  const [aadhaarErr, setAadhaarErr] = useState(false);

  const handleChange = (e) =>
    setSignup({ ...signup, [e.target.name]: e.target.value });

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    setLogo(file);
    setLogoPreview(URL.createObjectURL(file));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const formData = new FormData();
      formData.append('logo', logo);
      Object.keys(signup).forEach(function (key) {
        formData.append(key, signup[key]);
      });
      if (signup.password && signup.password === signup.conPassword) {
        if (signup.isHospitalAdmin === 'yes' && signup.isRahAdmin === 'yes') {
          setStatus({ error: 'Choose only one Admin' });
        } else {
          const verifiedAadhaar = verifyAadhaar(signup?.aadharCardNumber);
          if (verifiedAadhaar) {
            const res = await axios.post('/api/user/signup', formData, {
              headers: { 'Content-Type': 'multipart/form-data' },
            });
            console.log('signup completed');
            if (res.data) {
              setStatus({ success: 'Signup successful' });
              window.location.reload(false);
            } else {
              setStatus({ error: 'Unable to create user' });
            }
          } else {
            setAadhaarErr(true);
          }
        }
      } else {
        setStatus({ error: 'password did not matched' });
      }
    } catch (error) {
      setStatus({ error: error.response?.data?.error });
    }
  };

  return (
    <>
      <div className='my-modal'>
        <div className='button-group'>
          <button
            className={`navigation ${
              firstStepCompleted ? 'completed' : 'active'
            }`}
          >
            1
          </button>
          <div className='separator'></div>
          <button
            className={`navigation ${
              secondStepCompleted
                ? 'completed'
                : activeStep < 1
                ? 'none'
                : 'active'
            }`}
          >
            2
          </button>
          <div className='separator'></div>
          <button
            className={`navigation ${
              thirdStepCompleted
                ? 'completed'
                : activeStep < 2
                ? 'none'
                : 'active'
            }`}
          >
            3
          </button>
        </div>
        {activeStep === 0 && (
          <FirstPage
            signup={signup}
            handleChange={handleChange}
            setStatus={setStatus}
            setActiveStep={setActiveStep}
            setFirstStepCompleted={setFirstStepCompleted}
            setSecondStepCompleted={setSecondStepCompleted}
            setThirdStepCompleted={setThirdStepCompleted}
          />
        )}
        {activeStep === 1 && (
          <SecondPage
            signup={signup}
            handleChange={handleChange}
            setStatus={setStatus}
            handleFileChange={handleFileChange}
            logoPreview={logoPreview}
            setActiveStep={setActiveStep}
            setFirstStepCompleted={setFirstStepCompleted}
            setSecondStepCompleted={setSecondStepCompleted}
            setThirdStepCompleted={setThirdStepCompleted}
          />
        )}
        {activeStep === 2 && (
          <ThirdPage
            signup={signup}
            handleChange={handleChange}
            setStatus={setStatus}
            handleSubmit={handleSubmit}
            setActiveStep={setActiveStep}
            setFirstStepCompleted={setFirstStepCompleted}
            setSecondStepCompleted={setSecondStepCompleted}
            setThirdStepCompleted={setThirdStepCompleted}
            aadhaarErr={aadhaarErr}
          />
        )}
        {activeStep === 3 && (
          <Thankyou
            setActiveStep={setActiveStep}
            setFirstStepCompleted={setFirstStepCompleted}
            setSecondStepCompleted={setSecondStepCompleted}
            setThirdStepCompleted={setThirdStepCompleted}
          />
        )}
        {status.success && (
          <p className='text-success text-capitalize p-1 m-0'>
            <i class='fas fa-check'></i>&nbsp;{status.success}
          </p>
        )}
        {status.error && (
          <p className='text-danger text-capitalize p-1 m-0'>
            <i className='fas fa-exclamation-triangle'></i>&nbsp;
            {status.error}
          </p>
        )}
      </div>
    </>
  );
};

export default Register;
