import React, { useContext } from "react";
import "../styles/services.css";
import { Link } from "react-router-dom";
import { LocationContext } from "../../App";
import StarRatingComponent from "react-star-rating-component";
import RightArrow from "../img/Arrow1.png";
import LeftArrow from "../img/Arrow2.png";
import Slider from "react-slick";
function Services3({ hospitals }) {
  const { isCoords } = useContext(LocationContext);
  const ArrowLeft = ({ currentSlide, slideCount, ...props }) => (
    <img
      style={{ height: "50px", width: "50px" }}
      src={LeftArrow}
      alt="prevArrow"
      {...props}
    />
  );
  const ArrowRight = ({ currentSlide, slideCount, ...props }) => (
    <img src={RightArrow} alt="nextArrow" {...props} />
  );
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: hospitals && hospitals.length > 3 ? 3 : hospitals.length,
    swipeToSlide: true,
    initialSlide: 0,
    prevArrow: <ArrowLeft />,
    nextArrow: <ArrowRight />,

    responsive: [
      {
        breakpoint: 1224,
        settings: {
          slidesToShow:
            hospitals && hospitals.length > 2 ? 3 : hospitals.length,
          slidesToScroll: 2,
          infinite: true,
          dots: false,
        },
      },
      {
        breakpoint: 924,
        settings: {
          slidesToShow:
            hospitals && hospitals.length > 1 ? 2 : hospitals.length,
          slidesToScroll: 2,
          infinite: true,
          dots: false,
        },
      },

      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  return (
    <div className="service-3" style={{ display: "none" }}>
      <section className="services-area services-border pos-rel">
        <span className="h4services-bg">
          <img src="" alt="" />
        </span>
        <div className="container">
          <div className="row">
            <div className="col-xl-8 offset-xl-2 col-lg-10 offset-lg-1">
              <div className="section-title text-center pos-rel mb-75">
                <div className="section-text pos-rel">
                  <h1>Best Rated Hospitals</h1>
                  <h5>
                    Know all the top ranking hospitals in your state with
                    detailed overview and patients reviews, at a glance.
                  </h5>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <div className="slider slider-hospital">
        <div className="card-slider">
          <Slider className="Hospital-Cards" {...settings}>
            {hospitals &&
              hospitals
                .sort((a, b) => a?.rating - b?.rating)
                .map((hospital) => (
                  <div>
                    <div class="card-1" key={hospital?._id}>
                      <Link to={`/hospital/details/${hospital?._id}`}>
                        <div
                          className="banner-img"
                          style={{
                            opacity: !!hospital?.logo ? 1 : "0.7",
                            backgroundImage: `url(${
                              !!hospital?.logo
                                ? hospital?.logo.replace(/ /g, "%20")
                                : process.env.PUBLIC_URL +
                                  "/images/hospital_default_logo.png"
                            })`,
                          }}
                        ></div>
                      </Link>
                      <button className="profile-img">
                        {hospital?.hospital_type}
                      </button>
                      <h1 class="name">{hospital?.hospital_name}</h1>
                      <div className="hos-star-rating d-flex align-items-">
                        <StarRatingComponent
                          name="rate1"
                          starCount="5"
                          value={hospital?.rating}
                          className="star-icon"
                          editing={false}
                        />
                      </div>
                      {isCoords && hospital?.distance && (
                        <div className="card-content">
                          <img
                            className=""
                            src={
                              require("../img/rah-assets/location.png").default
                            }
                            alt=""
                          />
                          <div className="text">
                            {hospital?.distance} kms away
                          </div>
                        </div>
                      )}

                      <div className="card-content">
                        <img
                          className=""
                          src={require("../img/rah-assets/bed.png").default}
                          alt=""
                        />
                        <div className="text" style={{ width: "45%" }}>
                          {hospital?.total_seats} Hospital Beds
                        </div>
                      </div>
                      <div className="card-content">
                        <img
                          className="active"
                          src={require("../img/rah-assets/call.png").default}
                          alt=""
                        />

                        <a
                          className="text active"
                          href="tel:{hospital?.contact_number}"
                        >
                          {" "}
                          +91 {hospital?.contact_number}
                        </a>
                      </div>

                      <div className="card-partition">
                        <div className="explore-button">
                          <div>
                            <Link
                              className="button"
                              to={`/hospital/details/${hospital?._id}`}
                            >
                              Explore
                            </Link>{" "}
                          </div>
                          <div>
                            <img
                              src={
                                require("../img/rah-assets/arrow-down.png")
                                  .default
                              }
                              alt=""
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
          </Slider>
        </div>
      </div>
    </div>
  );
}

export default Services3;
