import React from 'react';
import MultiUploader from './ImageUploader/MultiImages';
import Uploader from './ImageUploader/Uploader';
import './HospitalImageUploader.css';
function HospitalImageUploader({ logo, images }) {
  return (
    <div className='HospitalImageUploader-Main'>
      <div className='HospitalImageUploader-Body'>
        <div className='Uploader-text'>Hospital Logo</div>
        <Uploader logo={logo} />
        <div className='Uploader-text-2'>Additional Hospital Images</div>
        <MultiUploader images={images} />
      </div>
    </div>
  );
}

export default HospitalImageUploader;
