import { useState } from "react";
import "../styles/ambulancemapbox.css";
import GoogleMapReact from "google-map-react";
import { GOOGLE_MAP_API_KEY } from "../../env";
import { Marker, Popup } from "../../Search/components/Marker";
import DirectionsOutlinedIcon from "@mui/icons-material/DirectionsOutlined";

export default function AmbulancMapbox({ ambulances, location, isCoords }) {
  const [selectedAmbulance, setSelectedAmbulance] = useState(null);
  const [selectedLoc, setSelectedLoc] = useState(null);

  const [defaultProps, setDefaultProps] = useState({
    center: {
      lat: location?.[0],
      lng: location?.[1],
    },
    zoom: 11,
  });

  const handleGetHospital = (id) => {
    const getSingleHospital =
      selectedAmbulance?._id === id
        ? null
        : ambulances.find((i) => i._id === id);
    setSelectedAmbulance(getSingleHospital);
  };

  const showPopupBox = (ambulance) => {
    setSelectedLoc(ambulance);
    // selectedAmbulance?.hospital_name == name && setSelectedLoc(ambulance);
  };
  const closePopupBox = () => setSelectedLoc(null);

  const showInMapClicked = (address) => {
    isCoords &&
      window.open(
        `https://www.google.com/maps/dir/?api=1&destination=${address}`
      );
  };

  return (
    <>
      <div className="Ambulance-map-box-main">
        <div className="Ambulance-map-box-child">
          <div className="Ambulance-map-box-left">
            <GoogleMapReact
              bootstrapURLKeys={{
                key: GOOGLE_MAP_API_KEY,
              }}
              defaultCenter={defaultProps.center}
              defaultZoom={defaultProps.zoom}
            >
              {ambulances &&
                !selectedAmbulance &&
                Array.isArray(ambulances) &&
                !!ambulances.length &&
                ambulances.map(
                  (ambulance) =>
                    !!ambulance?.location?.coordinates && (
                      <Marker
                        key={ambulance?._id}
                        lat={ambulance?.location?.coordinates?.[0]}
                        lng={ambulance?.location?.coordinates?.[1]}
                        hospital={ambulance}
                        setSelectedLoc={setSelectedLoc}
                      />
                    )
                )}
              {selectedAmbulance &&
                !!selectedAmbulance?.location?.coordinates && (
                  <Marker
                    lat={selectedAmbulance?.location?.coordinates?.[0]}
                    lng={selectedAmbulance?.location?.coordinates?.[1]}
                    setSelectedLoc={setSelectedLoc}
                    hospital={selectedAmbulance}
                  />
                )}
              {selectedLoc && (
                <Popup
                  lat={selectedLoc?.location?.coordinates?.[0]}
                  lng={selectedLoc?.location?.coordinates?.[1]}
                  name={selectedLoc?.org_name}
                  distance={selectedLoc?.distance}
                  address={`${selectedLoc?.address} ${selectedLoc?.pincode}`}
                  contact_number={selectedLoc?.contact_number}
                  setSelectedLoc={setSelectedLoc}
                />
              )}
            </GoogleMapReact>
          </div>
          <div className="Ambulance-map-box-right">
            <h2>Services near you</h2>
            <div className="body">
              {ambulances &&
                !!ambulances.length &&
                ambulances.map((ambulance) => (
                  <div
                    key={ambulance?._id}
                    onMouseEnter={() => showPopupBox(ambulance)}
                    onMouseLeave={closePopupBox}
                    onClick={() => handleGetHospital(ambulance?._id)}
                    className="Ambulance-mapbox-item Ambulance-mapbox-item-main"
                  >
                    <div className="lefter">
                      <h3>{ambulance?.org_name}</h3>
                      <h4>
                        <i class="fas fa-star"></i> 3
                      </h4>
                    </div>

                    <div className="righter">
                      <div
                        className="righter-img"
                        onClick={() =>
                          showInMapClicked(
                            `${ambulance?.address} ${ambulance?.pincode}`
                          )
                        }
                      >
                        <DirectionsOutlinedIcon className="directionIcon" />
                      </div>
                      <div className="righter-img-2">
                        <a href={`tel:${ambulance?.contact_number}`}>
                          <img
                            style={{ height: "24px", width: "24px" }}
                            src={
                              require("../../emergencyPage/img/phone.png")
                                .default
                            }
                            alt=""
                          />
                        </a>
                      </div>
                    </div>
                  </div>
                ))}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
