  const [selectedPart, setSelectedPart] = useState('');

  const handleClick = (val) => {
    setSelectedPart(val);
    setSelectedBodyPart(val);
  };

  useEffect(
    () => selectedBodyPart && setSelectedPart(selectedBodyPart),
    [selectedBodyPart]
  );

  return (
    <>
      <svg
        width='263'
        height='502'
        viewBox='0 0 263 502'
        fill='none'
        xmlns='http://www.w3.org/2000/svg'
      >
        {parts.map((part, indx) => (
          <path
            key={indx}
            onClick={() => handleClick(part.name)}
            onDoubleClick={() => handleSearchBodyPart(part.name)}
            id={part.name}
            d={part.d}
            fill={
              part.name.replace(/ +/g, '').toLowerCase() ===
              selectedPart.replace(/ +/g, '').toLowerCase()
                ? '#1e90ff'
                : part.fill
            }
            stroke={
              part.name.replace(/ +/g, '').toLowerCase() ===
              selectedPart.replace(/ +/g, '').toLowerCase()
                ? '#fff'
                : part.stroke
            }
          />
        ))}
      </svg>
    </>
  );
