import "../styles/SearchResult.css";
import CallIcon from "@mui/icons-material/Call";
import DirectionsOutlinedIcon from "@mui/icons-material/DirectionsOutlined";
import { Link } from "react-router-dom";
import StarRatingComponent from "react-star-rating-component";
import Modal from "react-modal";
Modal.setAppElement("#root");

function SearchCard({
  id,
  handleGetHospital,
  name,
  type,
  img,
  contact_number,
  isCoords,
  distance,
  address,
  rating,
  location,
  setSelectedLoc,
  selectedHospital,
}) {
  // const [isModalopen, setModalopen] = useState(false);

  const showPopupBox = () => {
    !selectedHospital &&
      setSelectedLoc({
        hospital_name: name,
        distance,
        location,
        address,
        contact_number,
        setSelectedLoc,
      });
    selectedHospital?.hospital_name == name &&
      setSelectedLoc({
        hospital_name: name,
        distance,
        location,
        address,
        contact_number,
        setSelectedLoc,
      });
  };

  const closePopupBox = () => setSelectedLoc(null);

  const showInMapClicked = () => {
    isCoords &&
      window.open(
        `https://www.google.com/maps/dir/?api=1&destination=${address}`
      );
  };

  return (
    <div className="search-card-link" onClick={() => handleGetHospital(id)}>
      <div
        onMouseEnter={showPopupBox}
        onMouseLeave={closePopupBox}
        className="SearchResultCard"
      >
        <div className="leftimg">
          <Link to={`/hospital/details/${id}`}>
            <div className="type">{type}</div>
            <img
              src={
                !!img
                  ? img
                  : process.env.PUBLIC_URL + "/images/hospital_default_logo.png"
              }
              style={{ opacity: !!img ? 1 : "0.7" }}
            />
          </Link>
        </div>
        <div className="right">
          <Link to={`/hospital/details/${id}`} className="hospital-name">
            {name}
          </Link>
          <div className="rating d-flex">
            <StarRatingComponent
              name={id}
              starCount="5"
              value={rating}
              className="star-icon"
              editing={false}
            />
            {/* <StarIcon className='star-icon' />
            <StarIcon className='star-icon' />
            <StarIcon className='star-icon' />
            <StarIcon className='star-icon' />
            <StarHalfIcon className='star-icon' /> */}
          </div>
          <div className="but-2">
            <div className="but-call">
              <CallIcon />
              {/* <CallIcon onClick={() => setModalopen(true)} /> */}
              {contact_number && (
                <div className="pop_over pop_top">
                  <a className="text active" href={`tel:${contact_number}`}>
                    <i class="fas fa-phone"></i> &nbsp;{contact_number}
                  </a>
                </div>
              )}
            </div>
            <div className="but-dir">
              <DirectionsOutlinedIcon
                onClick={showInMapClicked}
                style={{ cursor: "pointer" }}
              />
              {isCoords && !!distance && (
                <div className="pop_over pop_right" onClick={showInMapClicked}>
                  {distance}km away
                </div>
              )}
            </div>
            <div className="distance_showing"> {distance} km away</div>
          </div>
        </div>
      </div>
      {/* <Modal
        className='Signup-Modal'
        isOpen={isModalopen}
        onRequestClose={() => setModalopen(false)}
        style={{
          overlay: {
            backgroundColor: 'rgba(0,0,0,0.5)',
            width: '100%',
            display: 'flex',
            // opacity: 0.5,
          },
          content: {
            border: 'none',
            backgroundColor: 'white',
          },
        }}
      >
        <div>hello Modal</div>
      </Modal> */}
    </div>
  );
}

export default SearchCard;
