import React, { useLayoutEffect } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";
import "react-bootstrap-table2-paginator/dist/react-bootstrap-table2-paginator.min.css";
import Heading from "./Heading/Heading";
import SuperAdminDashboard from "./Dashboard/SuperAdminDashboard";
import PendingWishlist from "./PendingWishlist/SuperAdminPendingWishlist";
import Sidebar from "./SuperAdminSidebar/Sidebar.js";
import { Switch, Route, useHistory } from "react-router-dom";
import axios from "axios";
import Profile from "./Profile/Profile";
import FeedbackTable from "./Feedback/Feedback";
import AmbulanceTable from "../Ambulance Form/Ambulance Table/AmbulanceTable.js";

const AdminDashboard = () => {
  const history = useHistory();

  useLayoutEffect(() => {
    axios
      .get("/api/user/isLoggedIn")
      .then((res) => {
        !res.data?.isLoggedIn && history.push("/");
      })
      .catch((error) => {
        console.log("error: ", error.response);
        history.push("/");
      });
    return;
  }, []);

  return (
    <div>
      <div className="App">
        <div className="sidebar">
          <Sidebar />
        </div>
        <div className="head" style={{ overflowY: "auto" }}>
          <div
            style={{
              width: "100%",
              height: "80px",
              boxShadow: "1px 1px 1px 1px #d0d5dd",
              marginLeft: "1%",
            }}
          >
            <Heading />
            <br></br>

            <div>
              <Switch>
                <Route exact path="/admin" component={SuperAdminDashboard} />
                <Route
                  exact
                  path="/admin/dashboard/pending-wishlist"
                  component={PendingWishlist}
                />
                <Route
                  exact
                  path="/admin/dashboard/feedback"
                  component={FeedbackTable}
                />
                <Route
                  exact
                  path="/admin/dashboard/ambulance"
                  component={AmbulanceTable}
                />
                <Route
                  exact
                  path="/admin/dashboard/profile"
                  component={Profile}
                />
              </Switch>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminDashboard;
