import React from 'react';
import Slider from 'react-slick';
import '../Components/MoreImages.css';
import LeftArrow from '../images/Frame 8102.png';
import RightArrow from '../images/Frame 8103.png';
const SlickArrowLeft = ({ currentSlide, slideCount, ...props }) => (
  <img src={LeftArrow} alt='prevArrow' {...props} />
);

const SlickArrowRight = ({ currentSlide, slideCount, ...props }) => (
  <img src={RightArrow} alt='nextArrow' {...props} />
);
export default function MoreImage({ images }) {
  const settings = {
    dots: true,
    className: 'MoreImages center',
    infinite: true,

    slidesToShow: 1,
    slidesToScroll: 1,
    initialSlide: 0,
    nextArrow: <SlickArrowLeft />,
    prevArrow: <SlickArrowRight />,
    dotsClass: 'slick-dots custom-indicator',
    customPaging: function (i) {
      return (
        <img
          src={images[i]}
          alt='hi'
          style={{
            width: '110px',
            height: '130px',
            objectFit: 'cover',
          }}
        />
      );
    },
    responsive: [
      {
        breakpoint: 524,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
        },
      },
    ],
  };
  return (
    <>
      <Slider className='MoreImages' {...settings}>
        {images &&
          images.map((img, indx) => (
            <div key={indx}>
              <img style={{ height: '350px', width: '99%' }} src={img} alt='' />
            </div>
          ))}
      </Slider>
    </>
  );
}
