import React from 'react';
import MainNavbar from '../NavigationBar/Navbar';
import './createForm.css';
import { useState } from 'react';
import axios from 'axios';

function CreateForm() {
  const [ambulanceOrg, setAmbulanceOrg] = useState({
    org_name: '',
    org_type: '',
    email: '',
    total_ambulance: '',
    contact_number: '',
    address: '',
    pincode: '',
  });
  const [AmbulanceList, setAmbulanceList] = useState([]);
  const [AmbulanceDetails, setAmbulanceDetails] = useState({
    type: '',
    driver: '',
    phone: '',
    location: '',
    price: '',
  });
  const [error, setError] = useState({
    detailsErr: '',
    formErr: '',
  });
  const [success, setSuccess] = useState(false);

  const handleAmbulanceOrgChange = (e) => {
    setAmbulanceOrg({
      ...ambulanceOrg,
      [e.target.name]: e.target.value,
    });
  };

  const handleDetailsChange = (e) => {
    setAmbulanceDetails({
      ...AmbulanceDetails,
      [e.target.name]: e.target.value,
    });
  };

  const handleAddToList = (e) => {
    const { type, driver, phone, location, price } = AmbulanceDetails;
    if (
      type === '' ||
      driver === '' ||
      phone === '' ||
      location === '' ||
      price === ''
    ) {
      // throw error
      setError({ ...error, detailsErr: 'Fill all valid ambulance details.' });
    } else {
      setError({ ...error, detailsErr: '' });

      if (!Number(AmbulanceDetails.phone))
        return setError({
          ...error,
          detailsErr: 'Invalid ambulance phone number',
        });

      if (!Number(AmbulanceDetails.price))
        return setError({
          ...error,
          detailsErr: 'Invalid ambulance price',
        });

      setAmbulanceList([...AmbulanceList, AmbulanceDetails]);
      setAmbulanceDetails({
        type: '',
        driver: '',
        phone: '',
        location: '',
        price: '',
      });
    }
  };

  const handleFormSubmit = async (e) => {
    e.preventDefault();
    const {
      org_name,
      org_type,
      email,
      total_ambulance,
      contact_number,
      address,
      pincode,
    } = ambulanceOrg;
    try {
      if (
        org_name === '' ||
        org_type === '' ||
        email === '' ||
        total_ambulance === '' ||
        contact_number === '' ||
        address === '' ||
        pincode === ''
      ) {
        setSuccess(false);
        setError({ ...error, formErr: 'All fields are required.' });
      } else {
        setError({ detailsErr: '', formErr: '' });

        if (!Number(pincode))
          return setError({ ...error, formErr: 'Invalid pincode' });

        // If everything alright, send API request
        const createAmbulance = await axios.post('/api/ambulance/create', {
          ...ambulanceOrg,
          pincode,
          ambulance_details: AmbulanceList,
        });
        if (createAmbulance) {
          console.log(createAmbulance);
          setSuccess(true);
          window.location.reload(false);
        }
      }
    } catch (error) {
      console.log(error);
      setSuccess(false);
      setError({ ...error, formErr: 'Failed to save information!' });
    }
  };

  return (
    <>
      <MainNavbar />
      <form className='row CreateForm' onSubmit={handleFormSubmit}>
        <h3>Ambulance Admin Form</h3>
        <div className='FirstRow'>
          <div className='LeftColumn'>
            <div className='field'>
              <label for='inputEmail4' className='form-label'>
                Ambulance Organisation Name
              </label>
              <input
                type='text'
                name='org_name'
                value={ambulanceOrg.org_name}
                onChange={handleAmbulanceOrgChange}
                className='form-control'
                id='inputEmail4'
              />
            </div>
          </div>
          <div className='RightColumn'>
            <div className='field'>
              <label className='form-label'>Organisation Type </label>
              <select
                name='org_type'
                value={ambulanceOrg.org_type}
                onChange={handleAmbulanceOrgChange}
                className='form-select'
                aria-label='Default select example'
                required
              >
                <option value=''>Select</option>
                <option
                  value='govt'
                  selected={ambulanceOrg.org_type === 'govt'}
                >
                  Government
                </option>
                <option
                  value='private'
                  selected={ambulanceOrg.org_type === 'private'}
                >
                  Private
                </option>
              </select>
            </div>
          </div>
        </div>
        <div className='SecondRow'>
          <div className='LeftColumn'>
            <div className='field'>
              <label for='emailId' className='form-label'>
                Email Address
              </label>
              <input
                type='text'
                name='email'
                value={ambulanceOrg.email}
                onChange={handleAmbulanceOrgChange}
                className='form-control'
                id='inputCity'
              />
            </div>
          </div>
          <div className='RightColumn'>
            <div className='field rowL'>
              <label for='inputState' className='form-label'>
                Total Ambulance
              </label>
              <input
                type='number'
                name='total_ambulance'
                value={ambulanceOrg.total_ambulance}
                onChange={handleAmbulanceOrgChange}
                minLength={1}
                min={0}
                className='form-control'
                id='inputCity'
              />
            </div>
            <div className='rowL field'>
              <label for='inputZip' className='form-label'>
                Contact Number
              </label>
              <input
                type='text'
                name='contact_number'
                value={ambulanceOrg.contact_number}
                onChange={handleAmbulanceOrgChange}
                minLength={10}
                maxLength={14}
                className='form-control'
                id='inputZip'
              />
            </div>
          </div>
        </div>
        <div className='ThirdRow'>
          <div className='RightColumn'>
            <div className='rowL field'>
              <label for='inputEmail4' className='form-label'>
                Address
              </label>
              <input
                type='text'
                name='address'
                value={ambulanceOrg.address}
                onChange={handleAmbulanceOrgChange}
                className='form-control'
                id='inputEmail4'
              />
            </div>

            <div className='rowL field'>
              <label for='inputZip' className='form-label'>
                Pincode
              </label>
              <input
                type='text'
                name='pincode'
                value={ambulanceOrg.pincode}
                onChange={handleAmbulanceOrgChange}
                minLength={6}
                maxLength={6}
                className='form-control'
                id='inputZip'
              />
            </div>
          </div>
        </div>
        <div className='FourthRow'>
          <label for='inputState' className='form-label'>
            Ambulance Details
          </label>
          <div className='DetailsBox'>
            <div className='Ambulance-fields-1'>
              <input
                name='type'
                value={AmbulanceDetails.type}
                onChange={handleDetailsChange}
                type='text'
                className='form-control'
                id='inputCity'
                placeholder='Type'
              />
            </div>
            <div className='Ambulance-fields-2'>
              <input
                name='driver'
                value={AmbulanceDetails.driver}
                onChange={handleDetailsChange}
                type='text'
                className='form-control'
                id='inputCity'
                placeholder=" Driver's Name"
              />
            </div>
            <div className='Ambulance-fields-3'>
              <input
                name='phone'
                value={AmbulanceDetails.phone}
                onChange={handleDetailsChange}
                minLength={10}
                maxLength={14}
                type='text'
                className='form-control'
                id='inputCity'
                placeholder='Phone Number'
              />
            </div>
            <div className='Ambulance-fields-4'>
              <input
                name='location'
                value={AmbulanceDetails.location}
                type='text'
                className='form-control'
                id='inputCity'
                placeholder='General Location'
                onChange={handleDetailsChange}
              />
            </div>
            <div className='Ambulance-fields-5'>
              <input
                name='price'
                value={AmbulanceDetails.price}
                type='number'
                className='form-control'
                id='inputCity'
                placeholder='Price'
                onChange={handleDetailsChange}
                minLength={1}
                min={0}
              />
            </div>
          </div>
          <div className='AddDiv'>
            <div onClick={handleAddToList}>
              <i className='fas fa-plus-circle'></i>
            </div>
            <div className='Addtext'>
              Click on this + symbol to add the details of different types of
              Ambulances
            </div>
          </div>
          <p className='text-danger'>{error?.detailsErr}</p>
        </div>
        <div className='form-group w-100'>
          <div className='AmbulanceDetailsDiv'>
            {AmbulanceList &&
              AmbulanceList.map((item, index) => {
                return (
                  <div className='AmbulanceItems' key={index}>
                    <div className='details'>
                      <span>TYPE : </span>
                      {item.type}
                    </div>
                    <div className='details'>
                      <span>DRIVER : </span>
                      {item.driver}
                    </div>
                    <div className='details'>
                      <span>PHONE : </span>
                      {item.phone}
                    </div>
                    <div className='details'>
                      <span>LOCATION : </span>
                      {item.location}
                    </div>
                    <div className='details'>
                      <span>PRICE : </span>
                      {item.price}
                    </div>
                  </div>
                );
              })}
          </div>
        </div>
        <div className='FifthRow'>
          <div className='form-check  '>
            <input
              className='form-check-input'
              type='checkbox'
              id='gridCheck'
              style={{ width: '10px' }}
              required
            />
            <label className='form-check-label Checkboxtext' for='gridCheck'>
              I want to protect my data by signing an NDA
            </label>
          </div>
        </div>

        <p className='text-center text-danger'>{error?.formErr}</p>
        {success && (
          <p className='text-center text-success'>
            Ambulance info saved successfully!
          </p>
        )}

        <div
          className='col-12 d-flex AmbulanceFormButton'
          style={{ justifyContent: 'center' }}
        >
          <button type='submit' className='Submit-Button'>
            Submit
          </button>
        </div>
      </form>
    </>
  );
}
export default CreateForm;
