// import "../styles/searchbox.css";
import axios from 'axios';
import React, { useState, useEffect, useContext } from 'react';
import { useHistory } from 'react-router-dom';
import '../styles/emergencybanner.css';
import EmergencySearchBar from '../../searchBar/EmergencySearchbar';
import { LocationContext } from '../../App';

function EmergencyBanner({ setHospitals }) {
  const history = useHistory();
  const { address, location } = useContext(LocationContext);
  const [btnLoading, setBtnLoading] = useState(false);
  const [dropdown, setDropdown] = useState();
  const [searchField, setSearchField] = useState({
    location: '',
    search: '',
  });
  const handleSearchChange = (e) => {
    setSearchField({ ...searchField, [e.target.name]: e.target.value });
    e.target.name === 'search' &&
      axios
        .get(`/api/next/${e.target.value}`)
        .then((res) => {
          // console.log(res?.data);
          setDropdown(res?.data);
        })
        .catch((err) => console.log(err.response));
  };

  const handleDropdownClick = (e) => {
    setSearchField({ ...searchField, search: e.target.value });
    setDropdown();
  };

  const handleSearchSubmit = () => {
    if (searchField.location !== '' || searchField.search !== '') {
      setBtnLoading(true);

      axios
        .post(
          '/api/search/',
          {
            address: searchField.location,
            object: searchField.search,
          },
          {
            headers: {
              location: JSON.stringify(location),
            },
          }
        )
        .then((res) => {
          console.log(res.data?.list);
          setBtnLoading(false);
          // console.log(res.data.list);
          setHospitals(res?.data?.list);
          // history.push('/search', { list: res?.data?.list, searchField });
        })
        .catch((err) => {
          console.log(err.response);
          history.push('/search', {
            list: err.response.data.error,
            searchField,
          });
        });
    }
  };

  useEffect(() => {
    setSearchField({ ...searchField, location: address });
  }, [address]);

  return (
    <>
      <div className='EmergencyBanner-Main'>
        <div className='EmergencyBanner-body'>
          <div className='EmergencyBanner-heading'>
            Making emergency aid and healthcare information widely available.
          </div>
          <div className='Emergency-Searchbar'>
            <EmergencySearchBar
              btnLoading={btnLoading}
              dropdown={dropdown}
              searchField={searchField}
              handleSearchChange={handleSearchChange}
              handleDropdownClick={handleDropdownClick}
              handleSearchSubmit={handleSearchSubmit}
            />
          </div>
        </div>
      </div>
    </>
  );
}
export default EmergencyBanner;
