import React from 'react';
import { ArrowBack, InfoOutlined } from '@material-ui/icons';
import './Page.css';
import axios from 'axios';

const FirstPage = ({
  signup,
  handleChange,
  setStatus,
  setActiveStep,
  setFirstStepCompleted,
}) => {
  const handleContinue = (e) => {
    e.preventDefault();
    if (signup.password && signup.conPassword) {
      if (signup.password === signup.conPassword) {
        axios
          .get(`/api/user/isEmail/${signup?.email}`)
          .then((res) => {
            if (res.data?.allowEmail) {
              setActiveStep(1);
              setFirstStepCompleted(true);
              setStatus({ success: '', error: '' });
            } else {
              setStatus({ success: '', error: 'email already exists' });
            }
          })
          .catch((err) => console.log(err));
      } else {
        setStatus({ error: 'password did not matched' });
      }
    } else {
      setStatus({ error: 'fill password field' });
    }
  };

  return (
    <div className='signup_pages'>
      <div className='form-header'>
        {/* <ArrowBack className='icon back' /> */}
        <h2 className='form-heading form-heading-page-1'>Sign-Up</h2>
        <InfoOutlined className='icon info' />
      </div>
      <div className='second-header'>
        <h6>{'Login ID & Password'}</h6>
        <p>Enter your Email Address and Password.</p>
      </div>
      <div className='input-group'>
        <form method='POST' onSubmit={handleContinue}>
          <input
            name='email'
            value={signup?.email}
            onChange={handleChange}
            className='input'
            type='email'
            pattern='^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$'
            title='Invalid email'
            placeholder='Email Address*'
            required
          />
          <input
            name='password'
            minLength='4'
            value={signup?.password}
            onChange={handleChange}
            className='input'
            type='password'
            placeholder='Password*'
            required
          />
          <input
            name='conPassword'
            minLength='4'
            value={signup?.conPassword}
            onChange={handleChange}
            className='input'
            type='text'
            placeholder='Confirm Password*'
            required
          />
          <button type='submit' className='continue'>
            Continue
          </button>
        </form>
      </div>
    </div>
  );
};

export default FirstPage;
