import { useState, useEffect } from "react";
import FooterHospital from "./footer";
import AppointmentHospital from "./apppointment";
import Review from "./review";
import Department from "./department";
// import HospitalDoctor from './hospitaldoctor';
import Procedure from "./procedure";
import ValueService from "./valueservices";
// import Facilities from './facilities';
import Facilities2 from "./facilities2";
// import Hospital from './hospital';
import HeroSection2 from "./HeroSection2";
import axios from "axios";
import { useParams } from "react-router-dom";
import { Helmet } from "react-helmet";
import Navbar from "../../NavigationBar/Navbar";
import "../styles/main_hospital.css";
import Preloader from "../../Preloader";
import FooterCredit from "../../Footer_Credit/FooterCredit";

function Main_hospital() {
  const params = useParams();
  const [details, setDetails] = useState({});
  const [preloading, setPreloading] = useState(true);

  useEffect(() => {
    axios
      .get(`/api/hospital/details/${params?.id}`)
      .then((res) => {
        setDetails(res?.data?.hospital);
        setPreloading(false);
      })
      .catch((err) => console.log(err.response));
  }, [params?.id]);

  return (
    <div className="main_hospital">
      <Helmet>
        <title>Hospital Details</title>
      </Helmet>
      {preloading ? (
        <Preloader />
      ) : (
        <>
          <Navbar />
          <hr className="nav-line" />
          <HeroSection2 details={details} />
          {details?.facilities != 0 && (
            <Facilities2 facilities={details?.facilities} />
          )}
          <Department department={details?.department} />
          {/* <HospitalDoctor /> */}
          <Procedure />
          <ValueService />
          {/* <Review rating={details?.rating} reviews={details?.reviews} /> */}
          <AppointmentHospital />
          <FooterHospital />
          <FooterCredit />
        </>
      )}
    </div>
  );
}
export default Main_hospital;
