import React, { useState } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { SidebarData } from './HumanSidebarData';
import HumanSubMenu from './HumanSubMenu';
import { Container } from 'react-bootstrap';
// import Icon from "../../images/Group 7484.png";

const HumanNav = styled.div`
  background: #f1f1f1;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
`;

// const NavIcon = styled(Link)`
//   margin-left: 2rem;
//   font-size: 2rem;
//   height: 80px;
//   display: flex;
//   justify-content: flex-start;
//   align-items: center;
// `;

const HumanSidebarNav = styled.nav`
  ${'' /* background: #263544; */}
  ${'' /* width: 250px; */}
  overflow:scroll;
  height: 90vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: ${({ sidebar }) => (sidebar ? '0' : '-100%')};
  transition: 350ms;
  z-index: 10;
  margin-top: 80px;
  margin-left: 70px;
`;

const HumanSidebarWrap = styled.div`
  width: 100%;
`;

const Sidebar = () => {
  const [sidebar, setSidebar] = useState(true);

  // const showSidebar = () => setSidebar(!sidebar);

  return (
    <>
      <div>
        <HumanSidebarNav sidebar={sidebar}>
          <HumanSidebarWrap>
            {/* <NavIcon to="/"><img src={Icon} alt="icon" /></NavIcon> */}
            {SidebarData.map((item, index) => {
              return <HumanSubMenu item={item} key={index} />;
            })}
          </HumanSidebarWrap>
        </HumanSidebarNav>
      </div>
    </>
  );
};

export default Sidebar;
