import React, { useState, useEffect } from 'react';
import '../styles/HeroSection.css';
import SwipeToSlide from './Carousel/MultiItem';
import DefaultImage from '../img/rah-assets/default-hospial-image.jpg';
// import Facilities2 from '../components/facilities2';

// mui icons
import AirlineSeatFlatIcon from '@mui/icons-material/AirlineSeatFlat';
import BloodtypeIcon from '@mui/icons-material/Bloodtype';
import LocalPharmacyIcon from '@mui/icons-material/LocalPharmacy';
// import MailOutlineIcon from "@mui/icons-material/MailOutline";
import DirectionsIcon from '@mui/icons-material/Directions';
import GoogleMapReact from 'google-map-react';
import { Marker } from '../../Search/components/Marker';
import { Dropdown } from 'react-bootstrap';
import ShowMoreText from 'react-show-more-text';
import CallIcon from '@mui/icons-material/Call';

export default function HeroSection2({ details }) {
  const [defaultProps, setDefaultProps] = useState({
    center: {
      lat: details?.location?.coordinates?.[0],
      lng: details?.location?.coordinates?.[1],
    },
    zoom: 11,
  });

  const showInMapClicked = () => {
    window.open(
      `https://www.google.com/maps/dir/?api=1&destination=${details?.address}`
    );
  };

  useEffect(() => {
    details &&
      setDefaultProps({
        ...defaultProps,
        center: {
          lat: details?.location?.coordinates?.[1],
          lng: details?.location?.coordinates?.[0],
        },
      });
  }, [details]);

  return (
    <div className='herosection_main'>
      <div className='heroSection'>
        <div className='left'>
          <div className='col2'>
            {/* <div className='imgLg'>
              <img src={details?.logo} alt='' />
            </div> */}
          </div>
          <div className='colSlider'>
            <SwipeToSlide
              images={
                !!details?.images.length ? details.images : [DefaultImage]
              }
              className='col-slider-img'
            />
          </div>
        </div>
        <div className='right'>
          <div className='top'>
            <div className='greyish'>{details && details?.hospital_type}</div>
            <div className='title'>{details && details?.hospital_name}</div>
          </div>
          <div className='contact'>
            <Dropdown>
              <Dropdown.Toggle
                id='dropdown-basic'
                className='contact1 contactText1 align-items-center'
              >
                <div>
                  <CallIcon /> Contact us
                </div>
              </Dropdown.Toggle>

              <Dropdown.Menu style={{ boxShadow: '0 0 18px rgba(0,0,0,0.14' }}>
                <Dropdown.Item style={{ display: 'flex' }}>
                  <i className='fas fa-map-marker-alt'></i> &nbsp;
                  <p
                    style={{
                      maxWidth: '200px',
                      whiteSpace: 'normal',
                      textTransform: 'capitalize',
                      margin: '0',
                    }}
                  >
                    {details?.address}
                  </p>
                </Dropdown.Item>
                {details?.contact_number && (
                  <Dropdown.Item
                    href='#/action-2'
                    style={{
                      borderTop: '2px solid #eee',
                      borderBottom: '2px solid #eee',
                    }}
                  >
                    <a
                      className='text active text-decoration-none'
                      href={`tel:${details?.contact_number}`}
                    >
                      <i className='fas fa-phone'></i> &nbsp;
                      <strong>{details?.contact_number}</strong>
                    </a>
                  </Dropdown.Item>
                )}
                {details?.hospital_email && (
                  <Dropdown.Item href='#/action-2'>
                    <a
                      className='text active text-decoration-none'
                      href={`mailto:${details?.hospital_email}`}
                    >
                      <i className='fas fa-at'></i> &nbsp;
                      <strong>{details?.hospital_email}</strong>
                    </a>
                  </Dropdown.Item>
                )}
              </Dropdown.Menu>
            </Dropdown>
            <button className='contact2'>
              <div>
                <DirectionsIcon className='icon3' />
              </div>
              <div className='contactText1' onClick={showInMapClicked}>
                Get Directions
              </div>
            </button>
          </div>
          <div className='middle'>
            <div className='middle-left'>
              <ShowMoreText
                /* Default options */
                lines={2}
                more='Show more'
                less='Show less'
                className='content-css'
                anchorClassName='my-anchor-css-class'
                expanded={false}
              >
                <div className='description'>{details && details?.about}</div>
              </ShowMoreText>
            </div>
          </div>
          <div className='bottom'>
            <div className='menus1'>
              <div className='menu1'>
                <div className='icon1'>
                  <LocalPharmacyIcon className='iconDetail' />
                </div>
                <div className='feature'>Indoor Pharmacy</div>
              </div>
              <div className='menu1'>
                <div className='icon1'>
                  <AirlineSeatFlatIcon className='iconDetail' />
                </div>
                <div className='feature'>
                  {details && details?.total_seats}+ Beds
                </div>
              </div>
              <div className='menu1'>
                <div className='icon1'>
                  <BloodtypeIcon className='iconDetail' />
                </div>
                <div className='feature'>Blood Banks</div>
              </div>
            </div>
          </div>

          <div className='middle-right mt-2'>
            <p>{details && details?.address}</p>
          </div>

          <div className='middle-right'>
            <div className='map_area' style={{ width: '80%', height: '110px' }}>
              <GoogleMapReact
                bootstrapURLKeys={{
                  key: 'AIzaSyDyfP6ISLisAkBON9Faek0xDaoVPd2Eiu0',
                }}
                defaultCenter={defaultProps.center}
                defaultZoom={defaultProps.zoom}
              >
                {details?.location?.coordinates && (
                  <Marker
                    lat={details?.location?.coordinates?.[1]}
                    lng={details?.location?.coordinates?.[0]}
                  ></Marker>
                )}
              </GoogleMapReact>
            </div>
          </div>
        </div>
      </div>
      {/* <Facilities2 /> */}
    </div>
  );
}
