import { createContext, useContext, useEffect, useState } from 'react';
import Footer from '../../landingPage/components/footer';
import MainNavbar from '../../NavigationBar/Navbar';
import Appointment from '../../hospitalPage/components/apppointment';
import AmbulanceBanner from './ambulancebanner';
import AmbulancMapbox from './ambulancemapbox';
import AmbulanceHeader from './ambulance-header';
import AmbulanceTable from './ambulanceTable';
import FooterCredit from '../../Footer_Credit/FooterCredit';
import axios from 'axios';
import { LocationContext } from '../../App';

export const AmbulanceContext = createContext(null);

export default function MainAmbulance() {
  const { isCoords, location } = useContext(LocationContext);
  const [origAmbulances, setOrigAmbulances] = useState([]);
  const [ambulances, setAmbulances] = useState([]);

  useEffect(() => {
    axios
      .get('/api/ambulance/all', {
        headers: {
          location: JSON.stringify(location),
        },
      })
      .then((res) => {
        res.data?.ambulances.sort((a, b) => a?.distance - b?.distance);
        setAmbulances(res?.data?.ambulances);
        setOrigAmbulances(res?.data?.ambulances);
      })
      .catch((err) => console.log(err));
  }, [location]);

  return (
    <div>
      <AmbulanceContext.Provider
        value={{ origAmbulances, ambulances, setAmbulances }}
      >
        <MainNavbar />
        <AmbulanceBanner />
        <AmbulancMapbox
          ambulances={ambulances}
          isCoords={isCoords}
          location={location}
        />
        <AmbulanceHeader />
        <AmbulanceTable
          ambulances={ambulances}
          isCoords={isCoords}
          location={location}
        />
        <br />
        <br />
        <Appointment />
        <Footer />
        <FooterCredit />
      </AmbulanceContext.Provider>
    </div>
  );
}
