import React from "react";
import { CDBSidebarHeader, CDBSidebarMenuItem } from "cdbreact";
import { NavLink } from "react-router-dom";
import img1 from "../images/Frame 8087.png";
import img2 from "../images/Frame 8088.png";
import img3 from "../images/Frame 8089.png";
import img4 from "../images/Frame 8090.png";
import img5 from "../images/Group 7484.png";
import img6 from "../images/Frame 80881.png";
import img7 from "../images/Frame 80871.png";
import img8 from "../images/Frame 80911.png";
import img9 from "../images/Frame 80901.png";
import { useState } from "react";
import "./Sidebar.css";
import { OverlayTrigger, Tooltip } from "react-bootstrap";
import axios from "axios";
// import img10 from "../images/clipboard.svg";
// import img11 from "../images/clipboardW.svg";

const Sidebar = () => {
  const [showImage1, setShowImage1] = useState(false);
  // const [showImage2, setShowImage2] = useState(false);
  // const [showImage3, setShowImage3] = useState(true);
  // const [showImage4, setShowImage4] = useState(true);
  // const [showImage5, setShowImage5] = useState(true);

  const handleLogout = async () => {
    await axios.get("/api/user/logout");
    // history.push('/');
    window.location.href = "/";
  };

  const FeedBackWhite = {
    color: "#FEFEFF",
    backgroundColor: "#344054",
    fontSize: "26px",
    height: "40px",
    position: "relative",
    // left: "15px",
    padding: "8px",
    borderRadius: "4px",
  };
  const FeedBackBlack = {
    color: "#000",
    backgroundColor: "#fff",
    fontSize: "26px",
    height: "40px",
    position: "relative",
    // left: "15px",
    padding: "8px",
    borderRadius: "4px",
  };

  // const ImgS= <i class="fa-regular fa-clipboard"></i>

  const ActiveMenu = (menu) => {
    setShowImage1(menu);
  };

  return (
    <>
      <div
        style={{
          // display: "initial",
          // height: "100vh",
          overflow: "scroll initial",
        }}
      >
        <div
          style={{
            textColor: "#fff",
            minWidth: "110px",
            width: "10px",
            height: "100vh",
            // backgroundColor: "#263544",
          }}
        >
          <CDBSidebarHeader
            prefix={
              <i>
                <img
                  src={img5}
                  alt="hospital"
                  onClick={() => {
                    window.location.href = "/";
                  }}
                />
              </i>
            }
          ></CDBSidebarHeader>
          {/* <CDBSidebarContent className=""> */}
          {/* <CDBSidebarMenu> */}

          <NavLink exact to="/admin" activeClassName="" className="">
            {["Hospital"].map((placement) => (
              <OverlayTrigger
                key={placement}
                overlay={
                  <Tooltip id={`tooltip-${placement}`}>{placement}</Tooltip>
                }
              >
                <CDBSidebarMenuItem onClick={() => ActiveMenu("hospital")}>
                  {showImage1 === "hospital" ? (
                    <img src={img1} alt="hospital" />
                  ) : (
                    <img src={img7} alt="hospital" />
                  )}
                </CDBSidebarMenuItem>
              </OverlayTrigger>
            ))}
          </NavLink>
          <NavLink
            exact
            to="/admin/dashboard/pending-wishlist"
            activeClassName=""
            className=""
          >
            {["Wishlist"].map((placement) => (
              <OverlayTrigger
                key={placement}
                overlay={
                  <Tooltip id={`tooltip-${placement}`}>{placement}</Tooltip>
                }
              >
                <CDBSidebarMenuItem onClick={() => ActiveMenu("wishlish")}>
                  {showImage1 === "wishlish" ? (
                    <img src={img6} alt="wishlist" />
                  ) : (
                    <img src={img2} alt="wishlist" />
                  )}
                </CDBSidebarMenuItem>
              </OverlayTrigger>
            ))}
          </NavLink>
          {/* <i class='fa-solid fa-clipboard-list'></i> */}
          <NavLink exact to="/admin/dashboard/ambulance" activeClassName="">
            {["Ambulance"].map((placement) => (
              <OverlayTrigger
                key={placement}
                overlay={
                  <Tooltip id={`tooltip-${placement}`}>{placement}</Tooltip>
                }
              >
                <CDBSidebarMenuItem onClick={() => ActiveMenu("ambulance")}>
                  {showImage1 === "ambulance" ? (
                    <i
                      class="fa-solid fa-truck-medical"
                      style={FeedBackBlack}
                    ></i>
                  ) : (
                    <i
                      class="fa-solid fa-truck-medical"
                      style={FeedBackWhite}
                    ></i>
                  )}
                </CDBSidebarMenuItem>
              </OverlayTrigger>
            ))}
          </NavLink>
          <NavLink exact to="/admin/dashboard/feedback" activeClassName="">
            {["Feedback"].map((placement) => (
              <OverlayTrigger
                key={placement}
                overlay={
                  <Tooltip id={`tooltip-${placement}`}>{placement}</Tooltip>
                }
              >
                <CDBSidebarMenuItem onClick={() => ActiveMenu("feedback")}>
                  {showImage1 === "feedback" ? (
                    <i
                      class="fa-regular fa-clipboard"
                      style={FeedBackBlack}
                    ></i>
                  ) : (
                    <i
                      class="fa-regular fa-clipboard"
                      style={FeedBackWhite}
                    ></i>
                  )}
                </CDBSidebarMenuItem>
              </OverlayTrigger>
            ))}
          </NavLink>
          <NavLink
            exact
            to="/admin/dashboard/profile"
            activeClassName=""
            // onClick={() => {
            //   window.location.href = "https://rah108.in/admin";
            // }}
          >
            {["Profile"].map((placement) => (
              <OverlayTrigger
                key={placement}
                overlay={
                  <Tooltip id={`tooltip-${placement}`}>{placement}</Tooltip>
                }
              >
                {/* <img src={img3} alt="profile" /> */}
                <CDBSidebarMenuItem onClick={() => ActiveMenu("profile")}>
                  {showImage1 === "profile" ? (
                    <img src={img8} alt="profile" />
                  ) : (
                    <img src={img3} alt="profile" />
                  )}
                </CDBSidebarMenuItem>
              </OverlayTrigger>
            ))}
          </NavLink>
          <div onClick={handleLogout}>
            {["Logout"].map((placement) => (
              <OverlayTrigger
                key={placement}
                overlay={
                  <Tooltip id={`tooltip-${placement}`}>{placement}</Tooltip>
                }
              >
                <CDBSidebarMenuItem onClick={() => ActiveMenu("logout")}>
                  {showImage1 === "logout" ? (
                    <img src={img9} alt="logout" />
                  ) : (
                    <img src={img4} alt="logout" />
                  )}
                </CDBSidebarMenuItem>
              </OverlayTrigger>
            ))}
          </div>
          {/* </CDBSidebarMenu> */}
          {/* </CDBSidebarContent> */}
        </div>
      </div>
    </>
  );
};

export default Sidebar;
