import React, { useState } from "react";
import Feedback from "../../Feedback/Feedback";
import "../styles/emergency_call.css";

function Emergency_call() {
  return (
    <div>
      <div className="emergency_call">
        <img
          src={require("../img/rah-assets/phonecall White.png").default}
          className="eme-call-but"
        />
        <div className="overlay" style={{ width: "330%", height: "auto" }}>
          Helpline Number
          <br />
          <a href="tel:108">
            <i className="fas fa-phone-alt"></i>&nbsp;108
          </a>
        </div>
      </div>

      <Feedback className="feedbackModal" />
    </div>
  );
}

export default Emergency_call;
