import '../styles/Marker.scss';
import { useContext } from 'react';
import { LocationContext } from '../../App';
import ItemLocationIcon from '../assets/ItemLocation.svg';
import CallIcon from '@mui/icons-material/Call';
import DirectionsOutlinedIcon from '@mui/icons-material/DirectionsOutlined';

export const Popup = ({
  name,
  distance,
  address,
  contact_number,
  setSelectedLoc,
}) => {
  const { isCoords } = useContext(LocationContext);

  const showInMapClicked = () => {
    isCoords &&
      window.open(
        `https://www.google.com/maps/dir/?api=1&destination=${address}`
      );
  };

  return (
    <div className='popup_box'>
      <div className='heading_close'>
        <h3>{name}</h3>
        <div className='close' onClick={() => setSelectedLoc()}>
          <h2>&times;</h2>
        </div>
      </div>
      {isCoords && !!distance && <h5>{distance} Km away</h5>}
      <div className='contact_buttons'>
        <a href={`tel:${contact_number}`}>
          <div className='button'>
            <CallIcon />
          </div>
        </a>
        <div className='button dir' onClick={showInMapClicked}>
          <DirectionsOutlinedIcon />
        </div>
      </div>
    </div>
  );
};

export const Marker = ({ setSelectedLoc, hospital, isActive = false }) => {
  const handleClick = (e) => {
    e.preventDefault();
    setSelectedLoc(hospital);
  };
  return (
    <div className='marker' onClick={handleClick}>
      {/* <div className={`pointer ${isActive && 'pointer_active'}`}> */}
      <div className='pointer'>
        <img src={ItemLocationIcon} alt='marker' />
        {isActive && (
          <h2>
            <i className='fas fa-map-marker-alt text-danger'></i>
          </h2>
        )}
      </div>
    </div>
  );
};
