import React, { useState, useLayoutEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import "./Navbar.css";
import Modal from "react-modal";
import axios from "axios";
import { Tooltip } from "@material-ui/core";
import {
  InfoOutlined,
  VisibilityOffOutlined,
  VisibilityOutlined,
} from "@material-ui/icons";
import Register from "../SignUp_Login/Register";
import Form from "../SignUp_Login/Login/components/Form";

Modal.setAppElement("#root");

function MainNavbar() {
  const [openLinks, setOpenLinks] = useState(false);
  const [isLoginModalOpen, setIsLoginModalOpen] = useState(false);
  const [isSignUpModal, setIsSignUpModal] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [user, setUser] = useState();

  const toggleNavbar = () => {
    setOpenLinks(!openLinks);
  };
  const handleLinkClick = () => setOpenLinks(!openLinks);

  const handleLogout = async () => {
    await axios.get("/api/user/logout");
    // history.push('/');
    window.location.reload(false);
  };
  //login modal functionality
  const [login, setLogin] = useState({
    email: "",
    password: "",
  });
  const [status, setStatus] = useState({
    success: "",
    error: "",
  });

  const [isLock, setIsLock] = useState(true);

  const togglePassword = () => {
    setIsLock(!isLock);
  };

  const handleChange = (e) =>
    setLogin({ ...login, [e.target.name]: e.target.value });

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (login.email === "" || login.password === "") {
        setStatus({ error: "All fields are required" });
      } else {
        const res = await axios.post("/api/user/login", {
          ...login,
        });
        if (res?.data) {
          setStatus({ success: "Login successful" });
          // history.push('/');
          window.location.reload(false);
        } else {
          setStatus({ error: "Unable to login user" });
        }
      }
    } catch (error) {
      console.log(error.response);
      setStatus({ error: error.response.data.error });
    }
  };
  useLayoutEffect(() => {
    const isLoggedIn = async () => {
      try {
        const res = await axios.get("/api/user/isLoggedIn");
        if (res.statusText && res.data?.isLoggedIn) {
          setIsLoggedIn(true);
          setUser(res.data?.user);
        }
      } catch (error) {
        console.log("error: ", error.response);
      }
    };
    isLoggedIn();
    return;
  }, []);

  return (
    <>
      <div className="MainNavbar">
        <Link to="/">
          <div className="MainLogo">
            <img src={require("./img/logo.png").default} />
          </div>
        </Link>

        <div className="MainNav-menu">
          {openLinks ? (
            <div class="MainTesting-2">
              <Link onClick={handleLinkClick} to="/search">
                Hospitals
              </Link>
              {/* <Link onClick={handleLinkClick} to='/search'>
                Search
              </Link>
              <Link onClick={handleLinkClick} to='/'>
                Consultance
              </Link>
              <Link onClick={handleLinkClick} to='/'>
                Blood Banks
              </Link>
              <Link onClick={handleLinkClick} to='/'>
                Diagnostics
              </Link> */}
              <Link onClick={handleLinkClick} to="/ambulance">
                Ambulance
              </Link>
              <Link onClick={handleLinkClick} to="/emergency">
                Emergency
              </Link>
              {/* <Link onClick={handleLinkClick} to="/feedback">
                Feedback
              </Link> */}
              {/* <a><button class="search-btn"> <i class="fas fa-search"></i></button></a> */}
              {/* <a>
                  {' '}
                  <button href='#' className='login-btn'>
                    Login / Signup
                  </button>
                </a> */}
              {isLoggedIn ? (
                <>
                  <Link to="/admin">
                    <button className="login-btn me-1">Admin</button>
                  </Link>
                  <button onClick={handleLogout}>Logout</button>
                </>
              ) : (
                <button
                  className="login-btn"
                  onClick={() => {
                    setIsLoginModalOpen(true);
                    setIsSignUpModal(false);
                    setOpenLinks(!openLinks);
                  }}
                >
                  Login / Signup
                </button>
              )}
            </div>
          ) : (
            <div className="MainNav-cont">
              <div className="Maintopnav-2">
                <a href="/search">Hospitals</a>
                {/* <a href='/'>Consultance</a> */}
                {/* <a href='/'>Blood Banks</a> */}
                {/* <a href='/'>Diagnostics</a> */}
                <a href="/ambulance">Ambulance</a>
                <a href="/emergency">Emergency</a>
                {/* <a href="/feedback">Feedback</a> */}
              </div>

              <div className="Mainlogin-section align-items-center">
                <a href="/search">
                  <button class="search-btn">
                    {" "}
                    <i class="fas fa-search"></i>
                  </button>
                </a>
                {isLoggedIn ? (
                  <>
                    <div className="user_icon mx-2">
                      <i className="fas fa-user-circle"></i>
                      <div className="user_dropdown">
                        <Link to="/admin">Admin</Link>
                        <button
                          onClick={handleLogout}
                          style={{ fontSize: "1.1rem" }}
                        >
                          Logout
                        </button>
                      </div>
                    </div>
                  </>
                ) : (
                  <button
                    className="login-btn"
                    onClick={() => {
                      setIsLoginModalOpen(true);
                      setIsSignUpModal(false);
                    }}
                  >
                    Login / Signup
                  </button>
                )}
              </div>
            </div>
          )}

          {openLinks ? (
            <button className="ham-close" onClick={toggleNavbar}>
              <i className="fas fa-close"></i>
            </button>
          ) : (
            <button className="nav-button" onClick={toggleNavbar}>
              <i className="fas fa-bars"></i>
            </button>
          )}
        </div>
      </div>
      {/* Login Modal */}
      <Modal
        className="login-Modal"
        isOpen={isLoginModalOpen}
        onRequestClose={() => setIsLoginModalOpen(false)}
        style={{
          overlay: {
            backgroundColor: "rgba(0,0,0,0.5)",
            width: "100%",
            display: "flex",
          },
          content: {
            border: "none",
            background: "transparent",
            width: "40%",
            margin: "auto",
          },
        }}
      >
        <div className="sign-in" style={{ width: "100%" }}>
          <section style={{ width: "100%" }}>
            <div className="contentBx">
              <div className="formBx">
                <div className="header">
                  <h2>Login</h2>

                  <Tooltip
                    title="This is to keep data for easy service to you."
                    arrow
                  >
                    <InfoOutlined className="icons" />
                  </Tooltip>
                </div>
                <form method="POST" onSubmit={handleSubmit}>
                  <div className="inputBx">
                    <input
                      type="email"
                      name="email"
                      onChange={handleChange}
                      value={login.email}
                      placeholder="Email Address"
                      required
                    />
                  </div>
                  <div className="inputBx password-field">
                    <input
                      type={isLock ? "password" : "text"}
                      name="password"
                      onChange={handleChange}
                      value={login.password}
                      placeholder="Enter Password"
                      required
                    />
                    <span className="eye">
                      {isLock ? (
                        <VisibilityOutlined onClick={togglePassword} />
                      ) : (
                        <VisibilityOffOutlined onClick={togglePassword} />
                      )}
                    </span>
                  </div>

                  <div className="inputBx">
                    <p>
                      Don't have an account?&nbsp;
                      <a
                        onClick={() => {
                          setIsLoginModalOpen(false);
                          console.log(isSignUpModal);
                          setIsSignUpModal(true);
                          console.log("hi signupModal", isSignUpModal);
                        }}
                      >
                        Sign Up
                      </a>
                    </p>
                  </div>

                  <div className="inputBx">
                    <button type="submit" value="login">
                      Login
                    </button>
                  </div>
                  <div className="inputBx">
                    {status.success && (
                      <p className="text-success text-capitalize p-1 m-0">
                        <i class="fas fa-check"></i>&nbsp; {status.success}
                      </p>
                    )}
                    {status.error && (
                      <p className="text-danger text-capitalize p-1 m-0">
                        <i className="fas fa-exclamation-triangle"></i>&nbsp;
                        {status.error}
                      </p>
                    )}
                  </div>
                </form>
              </div>
            </div>
          </section>
        </div>
      </Modal>
      {/* Signup Modal */}
      <Modal
        className="Signup-Modal"
        isOpen={isSignUpModal}
        onRequestClose={() => setIsSignUpModal(false)}
        style={{
          overlay: {
            backgroundColor: "rgba(0,0,0,0.5)",
            width: "100%",
            display: "flex",
            // opacity: 0.5,
          },
          content: {
            border: "none",
            backgroundColor: "white",
          },
        }}
      >
        <Register />
      </Modal>
    </>
  );
}
export default MainNavbar;
