import React from "react";
import { Button } from "react-bootstrap";

function Heading() {
  return (
    <>
      <div
        style={{
          display: "flex",
          width: "100%",
          alignItems: "center",
        }}
        className="pt-3"
      >
        <div style={{ width: "70%" }} className="pl-3">
          <h5> Super Admin Dashboard</h5>
        </div>
        <div
          style={{
            width: "30%",
            display: "flex",
            justifyContent: "space-evenly",
          }}
        >
          <Button variant="outline-secondary" style={{ height: "40px" }}>
            <i class="fas fa-phone-volume" style={{ marginRight: "7px" }}></i>
            Contact Support
          </Button>

          <Button
            href="/admin/75ec639510609d/create/hospital"
            variant="outline-secondary"
            style={{ height: "40px" }}
          >
            <i class="fas fa-arrows-alt" style={{ marginRight: "7px" }}></i>
            Add hospital
          </Button>
        </div>
      </div>
    </>
  );
}

export default Heading;
