import Slider from 'react-slick';
import '../styles/facilities.css';
import LeftArrow from '../components/rah-assets/arrow-circle-right (1).png';
import RightArrow from '../components/rah-assets/arrow-circle-right.png';
import ShowMoreText from 'react-show-more-text';

export default function Facilities2({ facilities }) {
  const SlickArrowLeft = ({ currentSlide, slideCount, ...props }) => (
    <img src={LeftArrow} alt='prevArrow' {...props} />
  );
  const SlickArrowRight = ({ currentSlide, slideCount, ...props }) => (
    <img src={RightArrow} alt='nextArrow' {...props} />
  );
  // const [facilitiesData, setFacilitiesData] = useState([]);
  // useEffect(() => {
  //   setFacilitiesData([...facilities]);
  // }, [facilities]);

  const settings = {
    centerPadding: '60px',
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: facilities && facilities.length > 3 ? 4 : facilities.length,
    swipeToSlide: true,
    initialSlide: 0,
    prevArrow: <SlickArrowLeft />,
    nextArrow: <SlickArrowRight />,

    // customPaging: (i) => (
    //   <div
    //     style={{
    //       opacity: "0.6",
    //       border: "4px solid #C9FCFF",
    //       "&:hover": {
    //         opacity: "0.6",
    //         border: "4px solid black",
    //       },
    //     }}
    //   ></div>
    // ),
    responsive: [
      {
        breakpoint: 1224,
        settings: {
          slidesToShow:
            facilities && facilities.length > 2 ? 3 : facilities.length,
          slidesToScroll: 2,
          infinite: true,
          dots: false,
        },
      },
      {
        breakpoint: 924,
        settings: {
          slidesToShow:
            facilities && facilities.length > 1 ? 2 : facilities.length,
          slidesToScroll: 2,
          infinite: true,
          dots: false,
        },
      },

      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <div className='facility_section_main'>
      <div className='facilitiesSection'>
        <div className='header'>Facilities</div>
        <Slider {...settings}>
          {facilities &&
            !!facilities.length &&
            facilities.map((item, index) => (
              <div className='card1' key={index}>
                <div>
                  {/* <DescriptionIcon
                  className='icon5'
                  style={{ fontSize: '60px' }}
                /> */}
                  {item?.icon ? (
                    <img
                      className='icons_show mb-2'
                      style={{ height: '45px', width: '45px' }}
                      src={`${process.env.PUBLIC_URL}/images/facilities/${item?.icon}`}
                      alt='icon1'
                    />
                  ) : (
                    <i
                      className='fas fa-clinic-medical'
                      style={{
                        color: '#00acb2',
                        fontSize: '2.6rem',
                        paddingBottom: '.7rem',
                      }}
                    ></i>
                  )}

                  <div className='title3'>{item?.heading}</div>
                  <ShowMoreText
                    /* Default options */
                    lines={3}
                    more='Show more'
                    less='Show less'
                    className='content-css'
                    anchorClass='my-anchor-css-class'
                    expanded={false}
                  >
                    <div className='para3'>{item?.content}</div>
                  </ShowMoreText>
                </div>
              </div>
            ))}
        </Slider>
      </div>
    </div>
  );
}
