import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Hidden } from '@material-ui/core';
import { borderRadius } from '@mui/system';
import axios from 'axios';
import { React, useState } from 'react';
import { Form, Button, Row, Col, Modal } from 'react-bootstrap';
import './Feedback.css';
function Feedback() {
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const [feedback, setFeedback] = useState({
    rating: '',
    category: '',
    message: '',
  });

  const handleChange = (e) => {
    setFeedback({ ...feedback, [e.target.name]: e.target.value });
  };

  const handleCategoryChange = (inp) =>
    setFeedback({ ...feedback, category: inp });

  console.log(feedback);

  const handleFormSubmit = (e) => {
    e.preventDefault();
    axios
      .post('/api/feedback/new', feedback)
      .then((res) => {
        if (res?.data?.status) {
          setShow(false);
        }
      })
      .catch((err) => console.log(err));
  };

  return (
    <>
      <button
        onClick={handleShow}
        style={{
          width: ' 52px',
          top: '35%',
          right: ' 0px',
          fontFamily: ' Inter',
          color: 'whitesmoke',
          textAlign: 'center',
          backgroundColor: ' #8f49a0',
          borderRadius: ' 5px 0 0 5px',
          position: 'fixed',
          fontSize: '22px',
        }}
      >
        {/* <i class='fa-solid fa-clipboard-list'></i> */}
        <div className='Feedback_textheading'>
          <div className='Feedback_letters'>F</div>
          <div className='Feedback_letters'>E</div>
          <div className='Feedback_letters'>E</div>
          <div className='Feedback_letters'>D</div>
          <div className='Feedback_letters'>B</div>
          <div className='Feedback_letters'>A</div>
          <div className='Feedback_letters'>C</div>
          <div className='Feedback_letters'>K</div>
        </div>
      </button>
      {/* Model */}
      <div>
        <Modal style={{ overflowY: 'Hidden' }} show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title style={{ width: '100%' }}>
              <div className='FeedbackModal_Heading'>
                <div className='Feedback_logo'>
                  <img src={require('../NavigationBar/img/logo.png').default} />
                </div>

                <div className='Heading'>Your Feedback</div>
              </div>
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form>
              <Row>
                <div className='Feedback_div'>
                  <div className='feedback_heading'>
                    We would like your feedback to improve this website
                  </div>
                  <div className='feedback_heading'>
                    What is your opinion of this page ?
                  </div>
                  <div className='feedback_ratings_images'>
                    <label htmlFor='1'>
                      <input
                        type='radio'
                        name='rating'
                        onClick={handleChange}
                        value={1}
                        id='1'
                      />
                      <i
                        className={`far fa-angry ${
                          feedback.rating === '1' && 'active'
                        }`}
                      ></i>
                    </label>
                    <label htmlFor='2'>
                      <input
                        type='radio'
                        name='rating'
                        onClick={handleChange}
                        value={2}
                        id='2'
                      />
                      <i
                        className={`far fa-frown ${
                          feedback.rating === '2' && 'active'
                        }`}
                      ></i>
                    </label>
                    <label htmlFor='3'>
                      <input
                        type='radio'
                        name='rating'
                        onClick={handleChange}
                        value={3}
                        id='3'
                      />
                      <i
                        className={`far fa-meh ${
                          feedback.rating === '3' && 'active'
                        }`}
                      ></i>
                    </label>
                    <label htmlFor='4'>
                      <input
                        type='radio'
                        name='rating'
                        onClick={handleChange}
                        value={4}
                        id='4'
                      />
                      <i
                        className={`far fa-smile ${
                          feedback.rating === '4' && 'active'
                        }`}
                      ></i>
                    </label>
                    <label htmlFor='5'>
                      <input
                        type='radio'
                        name='rating'
                        onClick={handleChange}
                        value={5}
                        id='5'
                      />
                      <i
                        className={`far fa-laugh ${
                          feedback.rating === '5' && 'active'
                        }`}
                      ></i>
                    </label>
                  </div>
                </div>
              </Row>
              <Row>
                <div className='Feedback_category'>
                  <div className='Feedback_categoryheading'>
                    Did this website helpful to you.?
                  </div>
                  <div className='feedback_category'>
                    <div
                      onClick={() => handleCategoryChange('It still needs some work')}
                      className={`feedback_category_block ${
                        feedback.category === 'It still needs some work' &&
                        'bg-primary text-white'
                      }`}
                    >
                      It still needs some work
                    </div>
                    <div
                      onClick={() =>
                        handleCategoryChange('Yes, it is very helpful')
                      }
                      className={`feedback_category_block ${
                        feedback.category === 'Yes, it is very helpful' &&
                        'bg-primary text-white'
                      }`}
                    >
                      Yes, it is very helpful.
                    </div>
                    <div
                      onClick={() => handleCategoryChange('No, Its not helpful to me.')}
                      className={`feedback_category_block ${
                        feedback.category === 'No, Its not helpful to me.' &&
                        'bg-primary text-white'
                      }`}
                    >
                      No, Its not helpful to me.
                    </div>
                  </div>
                </div>
              </Row>
              {/* <Row>
                <Col>
                  <p
                    style={{
                      color: '#8e8e8e',
                    }}
                  >
                    Is it Helpful?
                  </p>
                  <select
                    name='helpful'
                    onChange={handleChange}
                    style={{
                      width: '30%',
                      top: ' 0px',
                      marginBottom: '6px',
                      color: '#8e8e8e',
                      height: '38%',
                      borderRadius: '5px',
                    }}
                  >
                    <option value=''>Select</option>
                    <option value='Yes'>Yes</option>
                    <option value='No'>No</option>
                  </select>
                </Col>
              </Row> */}
              <Form.Group
                className='mb-5'
                controlId='exampleForm.ControlTextarea1'
              >
                <Form.Label>How can we improve our Services?</Form.Label>
                <Form.Control
                  as='textarea'
                  name='message'
                  value={feedback.message}
                  onChange={handleChange}
                  rows={3}
                />
              </Form.Group>
            </Form>
            <div
              style={{
                width: '100%',
                display: 'flex',
                justifyContent: 'flex-end',
              }}
            >
              <Button
                className='FeedbackModal_SubmitButton'
                as='input'
                type='submit'
                value='Submit'
                onClick={handleFormSubmit}
              />
            </div>
          </Modal.Body>
        </Modal>
      </div>
    </>
  );
}

export default Feedback;
