import React from 'react';
import { Table } from 'react-bootstrap';
import axios from 'axios';
import { useState, useEffect } from 'react';
import { Button, InputGroup, FormControl, Modal } from 'react-bootstrap';
import '../App.css';
import WishlistToggleButton from './WishlistToggleButton';

function PendingWishlist() {
  const [filteredItems, setFilteredItems] = useState([]);
  const [search, setSearch] = useState('');
  const [items, setItems] = useState([]);

  const handleSearchFilter = (e) => {
    setSearch(e.target.value);
    if (!!e.target.value) {
      const filteredItems =
        !!items.length &&
        items.filter((item) =>
          item?.hospital_name.toLowerCase().includes(search)
        );
      // console.log('filtered ', filteredItems);
      setFilteredItems(filteredItems);
    } else {
      setFilteredItems(items);
    }
  };

  useEffect(() => {
    axios
      .get('/api/hospital/all')
      .then(function (response) {
        setItems(response.data.hospital);
        setFilteredItems(response.data.hospital);
      })
      .catch(function (error) {
        console.log(error);
      });
    return;
  }, []);
  return (
    <>
      <div className='main_box p-2 m-2 main_box_border'>
        <p>
          <span> Pending Waitlist</span>
        </p>

        <div className='main_button_end'>
          <InputGroup className='mb-3 main_button_search'>
            <FormControl
              value={search}
              onChange={handleSearchFilter}
              aria-label='Search'
              placeholder='Search'
              style={{ width: '100%' }}
            />
          </InputGroup>

          <Button
            variant='success'
            className='ml-2'
            style={{
              width: '12%',
              height: '34px',
            }}
          >
            <i className='fas fa-archive' style={{ marginRight: '10px' }}></i>
            Archive
          </Button>
        </div>

        <div className='pt-4' style={{ width: '100%' }}>
          <Table responsive>
            <thead>
              <tr>
                <th>Hospital Name</th>
                <th>Hospital Type</th>
                <th>Contact</th>
                <th>Data Posted</th>
                <th>Approval</th>
                <th></th>
              </tr>
            </thead>

            <tbody>
              {!!filteredItems.length &&
                filteredItems.map((item, key) => (
                  <WishlistToggleButton key={item?._id} item={item} />
                ))}
            </tbody>
          </Table>
        </div>
      </div>
    </>
  );
}

export default PendingWishlist;
