import axios from 'axios';
import React, { createContext, useContext, useState } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import '../styles/human_main.css';
import Dropdown from './Dropdown_human';
import Human from './Human_Body';
import { LocationContext } from '../../App';
import { useHistory } from 'react-router-dom';

export const BodyPartContext = createContext(null);

function HumanBody() {
  const history = useHistory();
  const { location } = useContext(LocationContext);
  const [selectedBodyPart, setSelectedBodyPart] = useState('');

  const handleSearchBodyPart = async (partName) => {
    partName &&
      axios
        .post(
          '/api/search/',
          {
            object: partName,
          },
          {
            headers: {
              location: JSON.stringify(location),
            },
          }
        )
        .then((res) => {
          if (res.data?.list) {
            console.log(res.data);
            history.push('/search', {
              list: res?.data?.list,
            });
          }
        })
        .catch((err) => {
          console.log(err.response);
          history.push('/search', {
            list: err.response.data.error,
          });
        });
  };

  return (
    <>
      <BodyPartContext.Provider
        value={{ selectedBodyPart, setSelectedBodyPart, handleSearchBodyPart }}
      >
        <div className='heading_human'>Choose your Problem Organ</div>
        <Container>
          <Row>
            <Col xs={6} md={4}>
              <Dropdown />
            </Col>
            <Col xs={12} md={8}>
              <Human />
            </Col>
          </Row>
        </Container>
      </BodyPartContext.Provider>
    </>
  );
}

export default HumanBody;
