import React from "react";
import { Button } from "react-bootstrap";

function Navbar() {
  return (
    <div
      style={{
        display: "flex",
        width: "100%",
        // height: "50%",
        alignItems: "center",
      }}
      className="pt-3"
    >
      <div style={{ width: "70%" }} className="pl-3">
        <h5>Admin Dashboard</h5>
      </div>
      <div
        style={{
          width: "30%",
          display: "flex",
          justifyContent: "space-evenly",
        }}
      >
        <Button variant="outline-secondary" style={{ height: "40px" }}>
          <i class="fas fa-phone-volume" style={{ marginRight: "7px" }}></i>
          Contact Support
        </Button>
      </div>
    </div>
  );
}

export default Navbar;
