// /* eslint-disable react/jsx-pascal-case */
import { useState, useEffect, useContext } from 'react';
// import Home from './home';
import Services1 from './services1';
import Services2 from './services2';
import Services3 from './services3';
import Doctor from './doctor';
import Footer from './footer';
// import Latest from './latest';
import Counter from './counter';
// import Testimonials from './testimonials';
import Emergency_call from './emergency_call';
import axios from 'axios';
import { Helmet } from 'react-helmet';
import { LocationContext } from '../../App';
import Home2 from './home2';
import Preloader from '../../Preloader';
import FooterCredit from '../../Footer_Credit/FooterCredit';

function Main_page() {
  const [preloading, setPreloading] = useState(true);

  const [hospitals, setHospitals] = useState([]);

  const { location } = useContext(LocationContext);

  // hide dropdown when clicked outside the box
  const handleDropdownClose = (setDropdown) => setDropdown();

  useEffect(() => {
    // get hospitals
    axios
      .get('/api/hospital/all?page=1', {
        headers: {
          location: JSON.stringify(location),
        },
      })
      .then((res) => {
        if (res?.data?.hospital) {
          setPreloading(false);
          setHospitals(res.data?.hospital);
        }
      })
      .catch((err) => console.log(err.response));
  }, [location]);

  return (
    <>
      <Helmet>
        <title>Welcome To RAH: Homepage</title>
      </Helmet>
      {preloading ? (
        <Preloader />
      ) : (
        <>
          <Emergency_call />
          <Home2 handleDropdownClose={handleDropdownClose} />
          <Doctor />
          <Services1 hospitals={hospitals} />
          <Services2 />
          <Services3 hospitals={hospitals} />
          {/* <Testimonials /> */}
          <Counter />
          {/* <Latest /> */}
          <Footer />
          <FooterCredit />
        </>
      )}
    </>
  );
}
export default Main_page;
