import React, { useEffect, useState } from 'react';
import SuperAdminDashboard from '../SuperAdminDashboard/AdminDashboard';
import CreateHospital from '../createHospital/CreateHospital';
import { useHistory, useParams } from 'react-router-dom';
import axios from 'axios';
import Preloader from '../Preloader';

const RenderAdmin = () => {
  const history = useHistory();
  const { hospital_id } = useParams();
  const [loading, setLoading] = useState(true);
  const [isAdmin, setIsAdmin] = useState();
  const [res, setRes] = useState();

  useEffect(() => {
    axios
      .get('/api/user/isLoggedIn')
      .then((res) => {
        !res.data?.isLoggedIn && history.push('/');
        setLoading(false);
        setRes(res.data);
        if (res.data?.user?.isRahAdmin) {
          setIsAdmin(true);
        }
      })
      .catch((error) => {
        console.log('error: ', error.response);
        history.push('/');
      });
    return;
  }, []);

  if (loading) {
    return <Preloader />;
  }
  if (isAdmin && hospital_id) {
    return <CreateHospital hospital_id={hospital_id} />;
  } else if (isAdmin) {
    return <SuperAdminDashboard />;
  } else {
    return <CreateHospital user={res?.user} />;
  }
};

export default RenderAdmin;
