import { Tooltip } from '@material-ui/core';
import {
  InfoOutlined,
  VisibilityOffOutlined,
  VisibilityOutlined,
} from '@material-ui/icons';
import React, { useState } from 'react';
import axios from 'axios';
import './login.css';
import Register from '../../Register/index';
import Modal from 'react-modal';
Modal.setAppElement('#root');

const Form = () => {
  const [login, setLogin] = useState({
    email: '',
    password: '',
  });
  const [status, setStatus] = useState({
    success: '',
    error: '',
  });

  const [isNewModalOpen, setIsNewModalOpen] = useState(false);
  const [isLock, setIsLock] = useState(true);

  const togglePassword = () => {
    setIsLock(!isLock);
  };

  const handleChange = (e) =>
    setLogin({ ...login, [e.target.name]: e.target.value });

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (login.email === '' || login.password === '') {
        setStatus({ error: 'All fields are required' });
      } else {
        const res = await axios.post('/api/user/login', {
          ...login,
        });
        console.log(res);
        if (res?.data) {
          setStatus({ success: 'Login successful' });
          window.location.reload(false);
        } else {
          setStatus({ error: 'Unable to login user' });
        }
      }
    } catch (error) {
      console.log(error.response);
      setStatus({ error: error.response.data.error });
    }
  };

  return (
    <div className='sign-in' style={{ width: '100%' }}>
      <section style={{ width: '100%' }}>
        <div className='contentBx'>
          <div className='formBx'>
            <div className='header'>
              <h2>Login</h2>

              <Tooltip
                title='This is to keep data for easy service to you.'
                arrow
              >
                <InfoOutlined className='icons' />
              </Tooltip>
            </div>
            <form method='POST' onSubmit={handleSubmit}>
              <div className='inputBx'>
                <input
                  type='email'
                  name='email'
                  onChange={handleChange}
                  value={login.email}
                  placeholder='Email Address'
                  required
                />
              </div>
              <div className='inputBx password-field'>
                <input
                  type={isLock ? 'password' : 'text'}
                  name='password'
                  onChange={handleChange}
                  value={login.password}
                  placeholder='Enter Password'
                  required
                />
                <span className='eye'>
                  {isLock ? (
                    <VisibilityOutlined onClick={togglePassword} />
                  ) : (
                    <VisibilityOffOutlined onClick={togglePassword} />
                  )}
                </span>
              </div>

              <div className='inputBx'>
                <p>
                  Don't have an account?&nbsp;
                  <a
                    onClick={() => {
                      setIsNewModalOpen(true);
                    }}
                  >
                    Sign Up
                  </a>
                </p>
              </div>

              <div className='inputBx'>
                <button type='submit' value='login'>
                  Login
                </button>
              </div>
              <div className='inputBx'>
                {status.success && (
                  <p className='text-success text-capitalize p-1 m-0'>
                    <i class='fas fa-check'></i>&nbsp; {status.success}
                  </p>
                )}
                {status.error && (
                  <p className='text-danger text-capitalize p-1 m-0'>
                    <i className='fas fa-exclamation-triangle'></i>&nbsp;
                    {status.error}
                  </p>
                )}
              </div>
            </form>
          </div>
        </div>
      </section>
      <Modal
        className='Signup-Modal'
        isOpen={isNewModalOpen}
        onRequestClose={() => setIsNewModalOpen(false)}
        style={{
          overlay: {
            backgroundColor: 'rgba(0,0,0,0.5)',
            width: '100%',
            display: 'flex',
            // opacity: 0.5,
          },
          content: {
            border: 'none',
            backgroundColor: 'white',
          },
        }}
      >
        <Register />
      </Modal>
    </div>
  );
};

export default Form;
